import { memo } from 'react';
import { View, type StyleProp, type ViewProps, type ViewStyle } from 'react-native';

interface Props extends ViewProps {
  size: number;
  direction?: 'vertical' | 'horizontal';
  style?: StyleProp<ViewStyle>;
}

/**
 * @public
 * @category UI
 * @name Spacing
 * @description
 * `Spacing`      .         .
 *
 * @param {object} [props] -   `props` .
 * @param {number} props.size -     .
 * @param {'vertical' | 'horizontal'} [props.direction = 'vertical'] -    .  `'vertical'`.
 * @param {StyleProp<ViewStyle>} [props.style] - `Spacing`   `style` .  `undefined`,     .
 *
 * @example
 * ### ,    `16`       
 *
 * ```tsx
 * import { View, Text } from 'react-native';
 * import { Spacing } from 'react-native-bedrock';
 *
 * export function SpacingExample() {
 *   return (
 *     <View>
 *       <Text>Top</Text>
 *       <Spacing size={16} direction="vertical" style={{ backgroundColor: 'red', width: 5 }} />
 *       <Text>Bottom,     </Text>
 *
 *       <View style={{ flexDirection: 'row' }}>
 *         <Text>Left</Text>
 *         <Spacing size={16} direction="horizontal" style={{ backgroundColor: 'red', height: 5 }} />
 *         <Text>Right,     </Text>
 *       </View>
 *     </View>
 *   );
 * }
 * ```
 */
export const Spacing = memo(function Spacing({ size, direction = 'vertical', style, ...restProps }: Props) {
  const directionStyle =
    direction === 'vertical'
      ? {
          height: size,
        }
      : {
          width: size,
        };

  return <View style={[directionStyle, style]} {...restProps} />;
});
