import { useSafeAreaInsets } from '@react-native-bedrock/native/react-native-safe-area-context';
/** @description iOS    SafeArea  */
export const IOS_NOTCH_SAFE_AREA_BOTTOM = 34;
/**
 * @name useSafeAreaBottom
 * @description SafeAreaBottom  .
 * - iOS   SafeArea Bottom    SafeArea Bottom  .
 * - MainTabBar     SafeArea   LayoutShift  .
 * - MainTabBar    SafeArea = 82px = 58px + 24px
 * - MainTabBar    SafeArea = 34px (  ,   0px)
 */
export function useSafeAreaBottom() {
    const safeAreaInsets = useSafeAreaInsets();
    const safeAreaBottom = Math.min(safeAreaInsets.bottom, IOS_NOTCH_SAFE_AREA_BOTTOM);
    return safeAreaBottom;
}
