import { Animated } from '../../interactions/animated';
import { generateHapticFeedback } from 'react-native-bedrock';
import { useCallback, useRef } from 'react';
import { Easing } from 'react-native';
export function useWiggleMotion() {
    const translateX = useRef(new Animated.Value(0)).current;
    const play = useCallback(() => {
        generateHapticFeedback({ type: 'wiggle' });
        Animated.sequence([
            Animated.timing(translateX, { toValue: 2, duration: 87.5, easing: Easing.linear, useNativeDriver: false }),
            Animated.timing(translateX, { toValue: -2, duration: 87.5, easing: Easing.linear, useNativeDriver: false }),
            Animated.timing(translateX, { toValue: 1, duration: 87.5, easing: Easing.linear, useNativeDriver: false }),
            Animated.timing(translateX, { toValue: -1, duration: 87.5, easing: Easing.linear, useNativeDriver: false }),
            Animated.timing(translateX, { toValue: 0, duration: 87.5, easing: Easing.linear, useNativeDriver: false }),
        ]).start();
    }, [translateX]);
    return {
        play,
        // TODO: style merge  
        style: {
            transform: [{ translateX }],
        },
    };
}
