import { useEffect } from 'react';
export const useTimer = ({ duration, callback }) => {
    useEffect(() => {
        const timeoutId = setTimeout(() => {
            callback();
        }, duration * 1000);
        return () => {
            clearTimeout(timeoutId);
        };
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
};
