export const bezier = {
    linear: [0, 0, 1, 1],
    ease: [0.6, 0, 0, 0.6],
    out: [0.25, 0.1, 0.25, 1],
    expo: [0.16, 1, 0.3, 1],
    back: [0.34, 1.56, 0.64, 1],
};
/**
 * cubic bezier array css easing   
 * @param bezier
 * @returns css cubic-bezier string
 */
export const bezierToCss = (bezier) => {
    return `cubic-bezier(${bezier.join(',')})`;
};
