import { useCallback, useEffect, useState } from 'react';
export function useAnimatePresence(visible, { enter, exit, onEntered, onExited }) {
    const [presenceState, setPresenceState] = useState('invisible');
    const open = useCallback(async () => {
        setPresenceState('visible-animation');
        await enter();
        setPresenceState('visible');
        onEntered?.();
    }, [enter, onEntered]);
    const close = useCallback(async () => {
        setPresenceState('invisible-animation');
        await exit();
        setPresenceState('invisible');
        onExited?.();
    }, [exit, onExited]);
    useEffect(() => {
        if (visible && presenceState !== 'visible') {
            open();
            return;
        }
        else if (!visible && presenceState !== 'invisible') {
            close();
        }
        // NOTE: visible  open, close 
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [visible]);
    return { presenceState };
}
