import { Linking } from 'react-native';

/**
 * @public
 * @kind function
 * @category  
 *
 * @name openURL
 * @signature
 * ```typescript
 * function openURL(url: string): Promise<any>;
 * ```
 *
 * @description
 *  URL      .
 *   `react-native` [`Linking.openURL`](https://reactnative.dev/docs/0.72/linking#openurl)   URL .
 *
 * @param {string} url   URL 
 * @returns {Promise<any>} URL     Promise
 *
 * @example
 *
 * ###  URL 
 *
 * ```tsx
 * import { openURL } from 'react-native-bedrock';
 * import { Button } from 'react-native';
 *
 * function Page() {
 *   const handlePress = () => {
 *     openURL('https://google.com');
 *   };
 *
 *   return <Button title="  " onPress={handlePress} />;
 * }
 * ```
 */
export function openURL(url: string): Promise<any> {
  return Linking.openURL(url);
}
