import { debounce } from 'es-toolkit';
import { PropsWithChildren, ReactElement, ReactNode, useContext, useEffect, useMemo, useState } from 'react';
import { StyleProp, View, ViewStyle } from 'react-native';
import { InView, IOContext } from '../intersection-observer';
import { noop } from '../utils/noop';
import { usePreservedCallback } from '../utils/usePreservedCallback';
import { useVisibility } from '../visibility';

interface Props {
  onImpressionStart?: () => void;
  onImpressionEnd?: () => void;
  enabled?: boolean;
  UNSAFE__impressFallbackOnMount?: boolean;
  style?: StyleProp<ViewStyle>;
  areaThreshold?: number;
  timeThreshold?: number;
  children?: ReactNode;
}

/**
 * @public
 * @category  
 * @name ImpressionArea
 * @description
 *         .                .
 *    `useVisibility`  (Viewport)     `IOScrollView` `InView`  . React `ScrollView`     , `ImpressionArea`          .

::: info 

`ImpressionArea`  `IOScrollView`   .  `IOScrollView`   , `UNSAFE__impressFallbackOnMount`  `true`        .   `false`   `IOScrollView`   `IOProviderMissingError` .

:::
 *
 * @param {() => void} [onImpressionStart]       .
 * @param {() => void} [onImpressionEnd]       .
 * @param {boolean} [enabled=true]      .  `true` . `false`     `onImpressionStart`    .
 * @param {number} [areaThreshold=0]     .        `onImpressionStart` .
 *  0 1   , 0   1px    . , 1   100%     .
 * @param {number} [timeThreshold=0]      `onImpressionStart`      . `0`(0).
 * @param {ViewStyle} [style] `InView`   `style` .  `undefined`, `style`    .
 * @param {boolean} [UNSAFE__impressFallbackOnMount=false]         .  `false`.
 * `IOScrollView`   ,  (Viewport)       .  , `IOScrollView`    `true`     .
 *
 * @returns {ReactElement} -        .
 * @example
 * 
 * ###   
 * 
 * ```tsx
 * import { useState } from 'react';
 * import { Button, Dimensions, Text, View } from 'react-native';
 * import { ImpressionArea, IOScrollView } from 'react-native-bedrock';
 * 
 * export default function ImpressionAreaExample() {
 *  const [isImpressionStart, setIsImpressionStart] = useState(false);
 * 
 *  return (
 *    <>
 *      <Text>{isImpressionStart ? 'Impression Start' : 'Impression End'}</Text>
 *        <IOScrollView
 *          style={{
 *            flex: 1,
 *            margin: 16,
 *            backgroundColor: 'white',
 *          }}
 *        >
 *        <View
 *          style={{
 *            height: Dimensions.get('screen').height,
 *            borderWidth: 1,
 *            borderColor: 'black',
 *          }}
 *        >
 *          <Text>Scroll to here</Text>
 *        </View>
 * 
 *        <ImpressionArea
 *          onImpressionStart={() => setIsImpressionStart(true)}
 *          onImpressionEnd={() => setIsImpressionStart(false)}
 *        >
 *          <Button title="Button" />
 *        </ImpressionArea>
 *      </IOScrollView>
 *    </>
 *  );
 * }
 * ```
 *
 * ###    
 * 
 * `ImpressionArea` `IOScrollView`      , `UNSAFE__impressFallbackOnMount` `true`        .
 * 
 * ```tsx
 * import { useState } from 'react';
 * import { Button, Dimensions, ScrollView, Text, View } from 'react-native';
 * import { ImpressionArea } from 'react-native-bedrock';
 * 
 * export default function ImpressionArea2Example() {
 *  const [isImpressionStart, setIsImpressionStart] = useState(false);
 * 
 *  return (
 *    <>
 *      <Text>{isImpressionStart ? 'Impression Start' : 'Impression End'}</Text>
 *      <ScrollView
 *        style={{
 *          flex: 1,
 *          margin: 16,
 *          backgroundColor: 'white',
 *        }}
 *      >
 *        <View
 *          style={{
 *            height: Dimensions.get('screen').height,
 *            borderWidth: 1,
 *            borderColor: 'black',
 *          }}
 *        >
 *          <Text>Scroll to here</Text>
 *        </View>
 * 
 *        <ImpressionArea
 *          UNSAFE__impressFallbackOnMount={true}
 *          onImpressionStart={() => setIsImpressionStart(true)}
 *          onImpressionEnd={() => setIsImpressionStart(false)}
 *        >
 *          <Button title="Button" />
 *        </ImpressionArea>
 *      </ScrollView>
 *    </>
 *  );
 * }
 * ```
 */
export function ImpressionArea(props: Props): ReactElement {
  const context = useContext(IOContext);

  if (context?.manager == null && props.UNSAFE__impressFallbackOnMount) {
    return <ImpressionAreaOnMount {...props} />;
  }

  return <ImpressionAreaWithIntersectionObserver {...props} />;
}

/**
 * @category Class
 * @kind class
 * @name IOProviderMissingError
 * @description
 * `ImpressionArea` `IOScrollView`      .
 *  `IOScrollView`     `ImpressionArea`     , `UNSAFE__impressFallbackOnMount` `true`  .
 */
export class IOProviderMissingError extends Error {
  message =
    'ImpressionArea IOContext.Provider  . ' +
    ' ScrollView @react-native-bedrock/core IOScrollView .';
}

/**
 * @category Components
 * @kind function
 * @name ImpressionAreaOnMount
 * @description
 *   (mount)  `onImpressionStart` `onImpressionEnd`  .
 * `ImpressionArea` `UNSAFE__impressFallbackOnMount` `true`  .
 * `IOScrollView`  `ImpressionArea`       .
 *
 *
 * @param {() => void} [onImpressionStart] -      .
 * @param {() => void} [onImpressionEnd] -      .
 * @param {StyleProp<ViewStyle>} [style] - `ImpressionAreaOnMount`     .
 * @param {ReactNode} [children] - `ImpressionAreaOnMount`    .
 * @returns {ReactElement} -     
 * @example
 * ###    
 * ```tsx
 * import { ImpressionAreaOnMount } from '@react-native-bedrock/core';
 *
 * export default function AlwaysImpressionAreaPage() {
 *   // AlwaysImpressionAreaPage mount  isImpressionStart true .
 *   const [isImpressionStart, setIsImpressionStart] = useState(false);
 *
 *   return (
 *     <ScrollView
 *       style={{
 *         flex: 1,
 *         padding: 16,
 *         backgroundColor: 'white',
 *       }}
 *     >
 *       <TestTitle title="ImpressionArea" description="@react-native-bedrock/impression-area" />
 *       <Button label={'Scroll to origin'} onPress={() => {}} />
 *       <Code style={{ width, height }} json={{ isImpressionStart }} />
 *       <ImpressionAreaOnMount
 *         onImpressionStart={() => setIsImpressionStart(true)}
 *         onImpressionEnd={() => setIsImpressionStart(false)}
 *       >
 *         <Button label={'Scroll to here'} onPress={() => {}} />
 *       </ImpressionArea>
 *     </ScrollView>
 *   );
 * }
 * ```
 */
export function ImpressionAreaOnMount({
  onImpressionStart: _onImpressionStart = noop,
  onImpressionEnd: _onImpressionEnd = noop,
  style,
  children,
}: PropsWithChildren<Props>): ReactElement {
  const visible = useVisibility();

  const isImpressed = visible;

  const onImpressionStart = usePreservedCallback(_onImpressionStart);
  const onImpressionEnd = usePreservedCallback(_onImpressionEnd);

  useEffect(() => {
    if (isImpressed) {
      onImpressionStart();
    } else {
      onImpressionEnd();
    }
  }, [isImpressed, onImpressionStart, onImpressionEnd]);

  return <View style={style}>{children}</View>;
}

/**
 * @category Components
 * @kind function
 * @name ImpressionAreaWithIntersectionObserver
 * @description
 *     `onImpressionStart` `onImpressionEnd`  .
 * `IOScrollView`    ,       .
 *
 * @param {() => void} [onImpressionStart] -      .
 * @param {() => void} [onImpressionEnd] -      .
 * @param {boolean} [enabled=true] - `ImpressionArea`   .
 * @param {StyleProp<ViewStyle>} [style] - `ImpressionAreaWithIntersectionObserver`     .
 * @param {number} [areaThreshold=0] -           .
 * @param {number} [timeThreshold=0] -      `onImpressionStart`, `onImpressionEnd`      .  `0`.
 * @param {ReactNode} [children] - `ImpressionAreaWithIntersectionObserver`      .
 * @returns {ReactElement} -       .
 * @example
 * ### IntersectionObserver  
 * ```tsx
 * import { ImpressionAreaWithIntersectionObserver, IOScrollView } from '@react-native-bedrock/core';
 *
 * export default function ImpressionAreaPage() {
 *   const [isImpressionStart, setIsImpressionStart] = useState(false);
 *
 *   return (
 *     <IOScrollView
 *       style={{
 *         flex: 1,
 *         margin: 16,
 *         backgroundColor: 'white',
 *       }}
 *     >
 *       <TestTitle title="ImpressionArea" description="@react-native-bedrock/impression-area" />
 *       <Button label={'Scroll to origin'} onPress={() => {}} />
 *       <Code style={{ width, height }} textStyle={{ flex: 1, verticalAlign: 'middle' }} json={{ isImpressionStart }} />
 *       <ImpressionAreaWithIntersectionObserver
 *         onImpressionStart={() => setIsImpressionStart(true)}
 *         onImpressionEnd={() => setIsImpressionStart(false)}
 *       >
 *         <Button label={'Scroll to here'} onPress={() => {}} />
 *       </ImpressionArea>
 *     </IOScrollView>
 *   );
 * }
 * ```
 */
export function ImpressionAreaWithIntersectionObserver({
  onImpressionStart: _onImpressionStart = noop,
  onImpressionEnd: _onImpressionEnd = noop,
  timeThreshold = 0,
  ...props
}: PropsWithChildren<{
  onImpressionStart?: () => void;
  onImpressionEnd?: () => void;
  enabled?: boolean;
  style?: StyleProp<ViewStyle>;
  areaThreshold?: number;
  timeThreshold?: number;
}>): ReactElement {
  const [isImpressed, setIsImpressed] = useState(false);

  const onImpressionChange = useMemo(() => {
    if (timeThreshold === 0) {
      return setIsImpressed;
    } else {
      return debounce(setIsImpressed, timeThreshold);
    }
  }, [setIsImpressed, timeThreshold]);

  const onImpressionStart = usePreservedCallback(_onImpressionStart);
  const onImpressionEnd = usePreservedCallback(_onImpressionEnd);

  useEffect(() => {
    if (isImpressed) {
      onImpressionStart?.();
    } else {
      onImpressionEnd?.();
    }
  }, [isImpressed, onImpressionStart, onImpressionEnd]);

  return <_ImpressionArea {...props} onImpressionChange={onImpressionChange} />;
}

// eslint-disable-next-line @typescript-eslint/naming-convention
function _ImpressionArea({
  children,
  onImpressionChange: _onImpressionChange = noop,
  enabled = true,
  areaThreshold = 0,
  style,
}: PropsWithChildren<{
  onImpressionChange?: (isImpressed: boolean) => void;
  enabled?: boolean;
  style?: StyleProp<ViewStyle>;
  areaThreshold?: number;
}>) {
  const visible = useVisibility();
  const [inviewImpressed, setInviewImpressed] = useState(false);

  const context = useContext(IOContext);

  if (context?.manager == null) {
    throw new IOProviderMissingError();
  }

  const impressed = visible && inviewImpressed && enabled;
  const onImpressionChange = usePreservedCallback(_onImpressionChange);

  useEffect(() => {
    onImpressionChange?.(impressed);
  }, [impressed, onImpressionChange]);

  return (
    <InView
      onChange={(inView, currentThreshold) => {
        const isVisible = inView && currentThreshold >= areaThreshold;
        setInviewImpressed(isVisible);
      }}
      style={style}
    >
      {children}
    </InView>
  );
}
