import * as adaptiveDictionary from './adaptiveDictionary.js';
/**
 * @description
 *   colors   .
 *
 * @example
 * const colors = colorPreference === 'light' ? colorsByPreference.light : colorsByPreference.dark;
 * <Txt color={colors.grey50} />
 */
export const colorsByPreference = {
    light: Object.fromEntries(Object.entries(adaptiveDictionary).map(([colorName, [lightColor]]) => {
        return [colorName, lightColor];
    })),
    dark: Object.fromEntries(Object.entries(adaptiveDictionary).map(([colorName, [, darkColor]]) => {
        return [colorName, darkColor];
    })),
};
