import {
  BottomSheet,
  Button,
  colors,
  Icon,
  ListRow,
  NumericSpinner,
  SegmentedControl,
  useBottomSheet,
  useToast,
} from "@toss-design-system/react-native";
import React, { MutableRefObject, useRef, useState } from "react";
import { Dimensions, Text, View } from "react-native";
import { BedrockRoute, useNavigation } from "react-native-bedrock";
import {
  GooglePlacesAutocomplete,
  GooglePlacesAutocompleteRef,
} from "react-native-google-places-autocomplete";
import { useAppDispatch, useAppSelector } from "store";
import { googleDetailApi, travelSliceActions } from "../../redux/travle-slice";

export const Route = BedrockRoute("/enroll/essential-search", {
  validateParams: (params) => params,
  component: EnrollEssentialSearch,
});

function EnrollEssentialSearch() {
  const [value, setValue] = useState("");
  const [hour, setHour] = useState(1);
  const dispatch = useAppDispatch();
  const bottomSheet = useBottomSheet();
  const { Place, essentialPlaces, accommodations } = useAppSelector(
    (state) => state.travelSlice
  );
  const params = Route.useParams();
  const autocompleteRef = useRef<GooglePlacesAutocompleteRef | null>();
  const SCREEN_WIDTH = Dimensions.get("window").width;
  const INPUT_WIDTH = SCREEN_WIDTH - 24 * 2; //   24
  const navigation = useNavigation();
  const [placeState, setPlaceState] = useState<{
    name: string | undefined;
    lat: number | undefined;
    lng: number | undefined;
    photo: string;
    category: number;
    takenTime: number;
    formatted_address: string | undefined;
    region: string;
  } | null>();
  const showHourBottomSheet = (datas: any) => {
    bottomSheet.open({
      children: (
        <HourBottomSheetContent
          initialHour={hour}
          onConfirm={(newHour) => {
            bottomSheet.close();
            addPlace(newHour, datas);
            //      (: dispatch)
          }}
          onCancel={() => {
            bottomSheet.close();
            autocompleteRef.current?.setAddressText("");
          }}
          placeType={value}
          placeState={datas}
        />
      ),
    });
  };
  const addPlace = (newHour, datas) => {
    const filteredPlaces = essentialPlaces.filter(
      (place) => place?.day === params.idx + 1
    );
    if (value == "" && accommodations[params.idx + 1].name != "") {
      open(" 1    ", {
        icon: "icon-warning-circle",
      });
      autocompleteRef.current?.setAddressText("");
    } else if (value == "" && filteredPlaces.length == 3) {
      open(" 3    ", {
        icon: "icon-warning-circle",
      });
      autocompleteRef.current?.setAddressText("");
    } else {
      let copy = [...(value == "" ? essentialPlaces : accommodations)];
      value == ""
        ? datas && (copy[params.idx + 1] = datas)
        : copy.push({
            ...datas,
            day: params.idx + 1,
            id: params.idx + 1 + "q" + datas?.lat,
            category: 5,
            takenTime: (newHour + 1) * 60,
          });
      dispatch(
        travelSliceActions.updateFiled({
          field: value == "" ? "accommodations" : "essentialPlaces",
          value: copy,
        })
      );
      navigation.goBack();
    }
  };
  const { open } = useToast();

  return (
    <>
      <SegmentedControl.Root
        size="large"
        alignment="fixed"
        name="segmented2"
        value={value}
        onChange={setValue}
      >
        <SegmentedControl.Item value=""></SegmentedControl.Item>
        <SegmentedControl.Item value=""></SegmentedControl.Item>
      </SegmentedControl.Root>

      <GooglePlacesAutocomplete
        placeholder={`${value} `}
        disableScroll={false}
        enablePoweredByContainer={false}
        keepResultsAfterBlur={true}
        ref={
          autocompleteRef as MutableRefObject<GooglePlacesAutocompleteRef | null>
        }
        query={{
          key: import.meta.env.GOOGLE_API_KEY,
          language: "ko",
        }}
        textInputProps={{
          placeholderTextColor: colors.grey500,
          allowFontScaling: false,
        }}
        renderLeftButton={() => <Icon name="icon-search-mono" />}
        styles={{
          container: { alignItems: "center", marginTop: 30 },
          textInputContainer: {
            width: INPUT_WIDTH,
            height: 44,
            borderRadius: 12,
            backgroundColor: colors.grey100,
            alignItems: "center",
            paddingLeft: 20,
          },
          listView: { width: INPUT_WIDTH, maxHeight: 250, zIndex: 1000 },
          textInput: {
            color: colors.grey500,
            backgroundColor: "transparent",
            flex: 0.9,
            fontSize: 16,
          },
          description: { color: "black" },
        }}
        fetchDetails={true}
        onPress={async (data, details) => {
          const placeId = details?.place_id;
          const response = await dispatch(
            googleDetailApi({ placeId: placeId })
          );
          let imageUrl;
          if (response.payload.result.photos) {
            const photoReference =
              response.payload.result?.photos[0]?.photo_reference;
            imageUrl = `https://maps.googleapis.com/maps/api/place/photo?maxwidth=400&photoreference=${photoReference}&key=${import.meta.env.GOOGLE_API_KEY}`;
          } else {
            imageUrl = null;
          }
          const datas = {
            ...Place,
            name: details?.name,
            lat: details?.geometry.location.lat,
            lng: details?.geometry.location.lng,
            formatted_address: details?.formatted_address.replace(
              " ",
              ""
            ),
            photo: imageUrl,
            region:
              regionOneList[details?.formatted_address.split(" ")[1]] +
              " " +
              (regionList[details?.formatted_address.split(" ")[1]] ??
                cityList[details?.formatted_address.split(" ")[2]]),
          };
          setPlaceState(datas);
          dispatch(travelSliceActions.enrollPlace(datas));

          showHourBottomSheet(datas);

          //      
          // dispatch(
          //   travelSliceActions.setDeparture({
          //     search: true,
          //     name: details?.name,
          //     lat: details?.geometry.location.lat,
          //     lng: details?.geometry.location.lng,
          //   })
          // );
        }}
        onFail={(error) => console.log(error)}
        onNotFound={() => console.log("no results")}
      />
    </>
  );
}

type HourBottomSheetContentProps = {
  initialHour: number;
  onConfirm: (newHour: number) => void;
  onCancel: () => void;
  placeType: string;
  placeState: any;
};

function HourBottomSheetContent({
  initialHour,
  onConfirm,
  onCancel,
  placeType,
  placeState,
}: HourBottomSheetContentProps) {
  const [localHour, setLocalHour] = useState(initialHour);

  return (
    <View>
      <ListRow
        contents={
          <ListRow.Texts
            type="2RowTypeA"
            top={placeState?.name}
            bottom={placeState?.formatted_address}
          />
        }
        right={
          <Button type="dark" size="tiny" style="weak" onPress={onCancel}>
            
          </Button>
        }
      />
      {placeType == "" && (
        <ListRow
          contents={<ListRow.Texts type="1RowTypeA" top=" " />}
          right={
            <NumericSpinner
              size="large"
              number={localHour}
              onNumberChange={(e) => {
                setLocalHour(e);
              }}
              maxNumber={3}
              minNumber={1}
            />
          }
        />
      )}

      <BottomSheet.CTA onPress={() => onConfirm(localHour)}>
        {placeType} 
      </BottomSheet.CTA>
    </View>
  );
}
