import type { NetworkStatus } from './types';
import { BedrockModule } from '../BedrockModule';

/**
 * @public
 * @category 
 * @kind function
 * @name getNetworkStatus
 * @description
 *       .
 *   `NetworkStatus` ,     (Wi-Fi,   ) .    .
 *
 * - `OFFLINE`:    .
 * - `WIFI`: Wi-Fi  .
 * - `2G`: 2G   .
 * - `3G`: 3G   .
 * - `4G`: 4G   .
 * - `5G`: 5G   .
 * - `WWAN`:  ,  (Wi-Fi, 2G~5G)    .   iOS   .
 * - `UNKNOWN`:       .      .
 *
 * @returns {Promise<NetworkStatus>}   .
 *
 * @example
 * ###    
 *
 *       .
 *
 * ```tsx
 * import { useState, useEffect } from 'react';
 * import { Text, View } from 'react-native';
 * import { getNetworkStatus, NetworkStatus } from 'react-native-bedrock';
 *
 * function GetNetworkStatus() {
 *   const [status, setStatus] = useState<NetworkStatus | ''>('');
 *
 *   useEffect(() => {
 *     async function fetchStatus() {
 *       const networkStatus = await getNetworkStatus();
 *       setStatus(networkStatus);
 *     }
 *
 *     fetchStatus();
 *   }, []);
 *
 *   return (
 *     <View>
 *       <Text>  : {status}</Text>
 *     </View>
 *   );
 * }
 * ```
 */
export function getNetworkStatus(): Promise<NetworkStatus> {
  return BedrockModule.getNetworkStatus();
}

/** export type */
export type { NetworkStatus };
