import { ComponentProps, ReactElement } from 'react';
import { Animated, View } from 'react-native';
import { useKeyboardAnimatedHeight } from './useKeyboardAnimatedHeight';

/**
 * @public
 * @category UI
 * @name KeyboardAboveView
 * @kind function
 * @description
 *           .
 *  ,    ""       .
 *
 * @param {StyleProp<ViewStyle>} [props.style] -     .  ,      .
 * @param {ReactNode} [props.children] -       .  , ,      .
 * @returns {ReactElement} -       [`Animated.View`](https://reactnative.dev/docs/animated#createanimatedcomponent) .
 * @example
 *
 * ###    
 *
 * ```tsx
 * import { ScrollView, TextInput, View, Text } from 'react-native';
 * import { KeyboardAboveView } from 'react-native-bedrock';
 *
 * export function KeyboardAboveViewExample() {
 *   return (
 *     <>
 *       <ScrollView>
 *         <TextInput placeholder="placeholder" />
 *       </ScrollView>
 *
 *       <KeyboardAboveView>
 *         <View style={{ width: '100%', height: 50, backgroundColor: 'yellow' }}>
 *           <Text>Keyboard  .</Text>
 *         </View>
 *       </KeyboardAboveView>
 *     </>
 *   );
 * }
 * ```
 */
export function KeyboardAboveView({ style, children, ...props }: ComponentProps<typeof View>): ReactElement {
  const keyboardHeight = useKeyboardAnimatedHeight();

  return (
    <Animated.View
      style={[
        style,
        {
          transform: [
            {
              translateY: Animated.subtract(0, keyboardHeight),
            },
          ],
        },
      ]}
      {...props}
    >
      {children}
    </Animated.View>
  );
}
