import { EmitterSubscription, NativeEventEmitter } from 'react-native';
import { EventEmitters } from './eventEmitters';
import { EventEmitterSchema } from './eventEmitters/types';
import { BedrockCoreModule } from '../native-modules';

type MapOf<T> = T extends EventEmitterSchema<infer K, any> ? { [key in K]: T } : never;
type UnionToIntersection<U> = (U extends any ? (k: U) => void : never) extends (k: infer I) => void ? I : never;
type EventEmittersMap = UnionToIntersection<MapOf<EventEmitters>>;
type EventKeys = keyof EventEmittersMap;
type ParamOf<K extends EventKeys> = EventEmittersMap[K]['params'];
/**
 * @interface BedrockEventEmitter
 * @description
 *      NativeEventEmitter      .
 * @property {(event: EventKeys, callback: (...params: ParamOf<E>) => void) => EmitterSubscription} addListener -    
 * @property {(subscription: EmitterSubscription) => void} removeSubscription -    
 */
interface BedrockEventEmitter {
  addListener<Event extends EventKeys>(
    event: Event,
    callback: (...params: ParamOf<Event>) => void
  ): EmitterSubscription;
}

/**
 * @kind constant
 * @name nativeEventEmitter
 * @description
 *     react-native NativeEventEmitter instance.
 * @type {BedrockEventEmitter}
 */
export const nativeEventEmitter = new NativeEventEmitter(BedrockCoreModule) as unknown as BedrockEventEmitter;
