import { useCallback, useMemo } from 'react';
import { BackHandler } from 'react-native';
import { useOverlayBase } from './useOverlayBase';
/**
 * @public
 * @tag UI
 * @category Hooks
 * @kind function
 * @name useOverlay
 * @description
 * `useOverlay`     Overlay       hook.  hook Overlay        .
 *
 * @returns {{
 *   open: (overlayElement: CreateOverlayElement) => void;
 *   close: () => void;
 * }}
 * `open`  `overlayElement`  Overlay , `close`   Overlay .
 *
 * @example
 * ```tsx
 * import { useOverlay } from '';
 *
 *  const overlay = useOverlay();
 *
 *   const openBottomSheet = useCallback(() => {
 *     return new Promise<void>((resolve) => {
 *       overlay.open(({ isOpen, exit, close }) => {
 *         const handleClose = () => {
 *           close();
 *           resolve();
 *         };
 *
 *         return (
 *           <BottomSheet
 *             open={isOpen}
 *             onClose={handleClose}
 *             onExited={exit}
 *             header={<BottomSheet.Header>BottomSheet V1 Header</BottomSheet.Header>}
 *             cta={<BottomSheet.CTA onPress={handleClose}></BottomSheet.CTA>}
 *           >
 *             <View>
 *               <TextField size="classic" label="name" value="FOCUS ME" />
 *             </View>
 *           </BottomSheet>
 *         );
 *       });
 *     });
 *   }, []);
 * ```
 */
export function useOverlay() {
    const overlay = useOverlayBase();
    const open = useCallback((overlayElement) => {
        function backHandler() {
            overlay.close();
            BackHandler.removeEventListener('hardwareBackPress', backHandler);
            return true;
        }
        BackHandler.addEventListener('hardwareBackPress', backHandler);
        overlay.open(({ ...props }) => {
            return overlayElement({
                ...props,
                close: () => {
                    BackHandler.removeEventListener('hardwareBackPress', backHandler);
                    props.close();
                },
            });
        });
    }, [overlay]);
    return useMemo(() => ({ open, close: overlay.close }), [open, overlay.close]);
}
