import { BedrockModule } from './BedrockModule';

/**
 * @public
 * @category  
 * @name setIosSwipeGestureEnabled
 * @description
 * `setIosSwipeGestureEnabled`  iOS        .
 *
 * @param {object} options      .
 * @param {boolean} options.isEnabled        . `true`     , `false`     .
 *
 * @returns {void}
 *
 * @example
 *
 * ### iOS      
 *
 * ** **       , ** **         .
 *
 *
 * ```tsx
 * import { setIosSwipeGestureEnabled } from 'react-native-bedrock';
 * import { Button } from 'react-native';
 *
 * function Page() {
 *  return (
 *    <>
 *     <Button title=" " onPress={() => setIosSwipeGestureEnabled({ isEnabled: false })} />
 *     <Button title=" " onPress={() => setIosSwipeGestureEnabled({ isEnabled: true })} />
 *    </>
 *  );
 * }
 * ```
 *
 */
export async function setIosSwipeGestureEnabled(options: { isEnabled: boolean }) {
  if (BedrockModule.setIosSwipeGestureEnabled == null) {
    return;
  }

  return BedrockModule.setIosSwipeGestureEnabled(options);
}
