export function calcRelativeOperator(baseValue, operator, value) {
    switch (operator) {
        case '-=': {
            return baseValue - value;
        }
        case '+=': {
            return baseValue + value;
        }
        case '*=': {
            return baseValue * value;
        }
        case '/=': {
            return baseValue / value;
        }
    }
}
