const THEME_COLOR_RANGES = [
    { name: 'red', min: 0, max: 30 },
    { name: 'orange', min: 30, max: 70 },
    { name: 'yellow', min: 70, max: 110 },
    { name: 'green', min: 110, max: 180 },
    { name: 'teal', min: 180, max: 220 },
    { name: 'blue', min: 220, max: 290 },
    { name: 'purple', min: 290, max: 340 },
    { name: 'red', min: 340, max: 360 },
];
/**
 * OKLCH   ThemeColorCode 
 */
export const getThemeColorCodeFromOklch = ({ c, h }) => {
    if (c <= 0.05) {
        return 'grey';
    }
    for (const { name, min, max } of THEME_COLOR_RANGES) {
        if (h > min && h <= max) {
            return name;
        }
    }
    return 'grey';
};
