import { useCallback, useRef } from 'react';
import { Animated } from 'react-native';
const WIGGLE_TRANSLATE_X_ARRAY = [0, 0.2, -0.2, 0.1, -0.1, 0];
export default function useWiggle() {
    const wiggleTranslateXRef = useRef(new Animated.Value(0)).current;
    const startWiggle = useCallback(() => {
        Animated.sequence(WIGGLE_TRANSLATE_X_ARRAY.map(x => {
            return Animated.timing(wiggleTranslateXRef, {
                toValue: x,
                duration: 60,
                useNativeDriver: true,
            });
        })).start();
    }, []);
    return {
        startWiggle,
        style: {
            transform: [{ translateX: wiggleTranslateXRef }],
        },
    };
}
