import { Spring } from './spring.js';
export function getSpringEasing({ stiffness = 100, damping = 10, mass = 1, velocity = 0.0 }) {
    const spring = new Spring({
        stiffness,
        damping,
        mass,
        velocity,
    });
    const duration = spring.totalDuration;
    const easeFunction = spring.getEaseFunction();
    return {
        duration,
        easeFunction,
    };
}
