export function clamp(value, min = -Infinity, max = Infinity) {
    return Math.min(Math.max(value, min), max);
}
/**
 * HTML input range   
 * min, max   min/max ,   min   step   
 */
export function getNearestSteppedValue(value, min, max, step) {
    const remainder = (value - min) % step;
    const offset = Math.sign(remainder) * (Math.abs(remainder) < step / 2 ? 0 : step);
    const others = Math.floor((value - min) / step) * step;
    const output = min + others + offset;
    return clamp(output, min, max);
}
