/**
 * framer-motion, gsap  custom ease function     
 * ease function duration 
 */
export const springEaseWithDuration = Object.fromEntries(Object.entries(spring).map(([springType, spring]) => {
    const springEasing = getSpringEasing(spring);
    return [
        springType,
        {
            ...springEasing,
            ease: springEasing.easeFunction,
        },
    ];
}));
