import { NativeStackNavigationOptions } from '@react-native-bedrock/native/@react-navigation/native-stack';
import { ComponentType } from 'react';

/**
 * @name Screen
 * @description    Screen
 * @example
 *
 * ```ts
 * function Page() {
 *  // ...
 * }
 *
 * Page.screenOptions = {
 *   // ...
 * }
 * ```
 */
export type Screen = ComponentType & BedrockScreenOptions;

interface BedrockScreenOptions {
  /** @description NativeStack Screen  screenOptions      */
  screenOptions?: NativeStackNavigationOptions;
}
