import { useCallback, useEffect, useRef } from 'react';
import { spring } from '@toss-design-system/easings';
import { Animated } from '../../../interactions/animated';
export const useToastAnimation = ({ position, initialY }) => {
    const opacity = useRef(new Animated.Value(0)).current;
    const translateY = useRef(new Animated.Value(initialY ?? 0)).current;
    const enterAnimationStartedRef = useRef(false);
    const enter = useCallback(() => {
        return new Promise(resolve => {
            if (initialY === undefined) {
                resolve();
                return;
            }
            translateY.setValue(initialY);
            enterAnimationStartedRef.current = true;
            Animated.parallel([
                Animated.spring(opacity, {
                    toValue: 1,
                    useNativeDriver: true,
                    ...(position === 'bottom' ? spring.quick : spring.small),
                }),
                Animated.spring(translateY, {
                    toValue: 0,
                    useNativeDriver: true,
                    ...(position === 'bottom' ? spring.quick : spring.small),
                }),
            ]).start(() => {
                resolve();
            });
        });
    }, [initialY, opacity, position, translateY]);
    useEffect(() => {
        if (!enterAnimationStartedRef.current && initialY != null) {
            enter();
        }
    }, [initialY, enter]);
    const exit = () => {
        return new Promise(resolve => {
            if (initialY == null) {
                resolve();
                return;
            }
            Animated.parallel([
                Animated.spring(opacity, {
                    toValue: 0,
                    useNativeDriver: true,
                    ...spring.quick,
                }),
                Animated.spring(translateY, {
                    toValue: initialY,
                    useNativeDriver: true,
                    ...spring.quick,
                }),
            ]).start(() => {
                resolve();
            });
        });
    };
    const animatedStyle = { opacity, transform: [{ translateY }] };
    return {
        enter,
        exit,
        animatedStyle,
    };
};
