import { KeyboardStatusLevel, useKeyboardStatusLevel, useSafeAreaBottom } from '../../utils';
/**
 * @name useBottomCTAContainerStyle
 * @description BottomCTA   
 */
export function useBottomCTAContainerStyle({ type = 'single', keyboardStyleEnabled = false }) {
    const safeAreaBottom = useSafeAreaBottom() ?? 0;
    const status = useKeyboardStatusLevel();
    const keyboardWillShow = status >= KeyboardStatusLevel.WillShow;
    const keyabordShownBottomStyle = type === 'single'
        ? {
            paddingHorizontal: 0,
            paddingBottom: 0,
        }
        : {
            paddingHorizontal: 20,
            paddingBottom: 18,
        };
    const containerStyle = keyboardStyleEnabled && keyboardWillShow
        ? keyabordShownBottomStyle
        : {
            paddingHorizontal: 20,
            paddingBottom: Math.max(safeAreaBottom, 18),
        };
    return containerStyle;
}
