import LottieView from '@react-native-bedrock/native/lottie-react-native';
import type { ComponentProps } from 'react';
import { View } from 'react-native';
import { ensureSafeLottie, hasFonts } from './ensureSafeLottie';
import { useFetchResource } from './useFetchResource';

type LottieReactNativeProps = ComponentProps<typeof LottieView>;
type BedrockSpecificLottieProps = {
  /**
   *     height  .
   */
  height: number | '100%';
  width?: number | '100%';
  maxWidth?: number;
  src: string;
};

type Props = BedrockSpecificLottieProps & Omit<LottieReactNativeProps, 'source'>;

/**
 * @public
 * @category UI
 * @name Lottie
 * @description `Lottie`   Lottie    .   Lottie JSON    .        .
 * @link https://github.com/lottie-react-native/lottie-react-native/blob/master/docs/api.md
 *
 * @param {object} [props] -   `props` . `lottie-react-native`  `LottieView`      .
 * @param {number | '100%'} [props.width] - Lottie  .
 * @param {number | '100%'} props.height - Lottie  .       .
 * @param {string} props.src - Lottie JSON  URL.    `LottieView` `source`   . `src`  .
 * @param {number} [props.maxWidth] - Lottie   .
 * @param {boolean} [props.autoPlay=true] -      .
 * @param {number} [props.speed=1] -   .
 * @returns {ReactElement} `lottie-react-native`  `LottieView`  .
 *
 * @example
 *
 * ### Lottie  
 *
 *   Lottie  ,          .
 *
 * ```tsx
 * import { Lottie } from '@react-native-bedrock/core';
 *
 * export function LottieExample() {
 *  return (
 *    <Lottie
 *      height={100}
 *      src="https://my-lottie-animation-url.com"
 *      autoPlay={true}
 *      loop={false}
 *      onAnimationFailure={() => {
 *        console.log('Animation Failed');
 *      }}
 *      onAnimationFinish={() => {
 *        console.log('Animation Finished');
 *      }}
 *    />
 *  );
 * ```
 */
export function Lottie({
  width,
  maxWidth,
  height,
  src,
  autoPlay = true,
  speed = 1,
  style,
  onAnimationFailure,
  ...props
}: Props) {
  const jsonData = useFetchResource(src, onAnimationFailure);
  //     
  if (jsonData == null) {
    return <View testID="lottie-placeholder" style={[{ opacity: 1, width, height }, style]} />;
  }

  if (hasFonts(jsonData) && __DEV__) {
    throw new Error(
      `Lottie       .   . source: ${src}`
    );
  }

  return (
    <LottieView
      source={ensureSafeLottie(jsonData)}
      autoPlay={autoPlay}
      speed={speed}
      style={[{ width, height, maxWidth }, style]}
      onAnimationFailure={onAnimationFailure}
      {...props}
    />
  );
}
