import type { BlurViewProps as InternalBlurViewProps } from '@react-native-bedrock/native/@react-native-community/blur';
import { View, ViewProps } from 'react-native';
import { ReactNativeBlurModule } from './ReactNativeBlurModule';
import { isBlurNativeModuleSupported } from './constants';

export type BlurType = InternalBlurViewProps['blurType'];

export interface BlurViewProps extends ViewProps {
  blurType?: BlurType;
  blurAmount?: number;
  /**
   * Vibrancy Effect 
   * @see https://zeddios.tistory.com/1140
   * @ref https://developer.apple.com/documentation/uikit/uivibrancyeffect
   */
  vibrancyEffect?: boolean;
  reducedTransparencyFallbackColor?: string;
}

/**
 * @public
 * @category UI
 * @name BlurView
 * @description
 * `BlurView`  iOS    UI  .     . iOS 
 *  Android  [`View`](https://reactnative.dev/docs/0.72/view)  .
 *
 *     , [Vibrancy ](https://developer.apple.com/documentation/uikit/uivibrancyeffect?language=objc)   .     [`reducedTransparencyFallbackColor`](https://github.com/Kureev/react-native-blur/tree/v4.3.2?tab=readme-ov-file#blurview)     .
 *
 * `isSupported`         . iOS 5.126.0    , Android  .
 *
 * @param {BlurViewProps} [props] props `BlurView`  . `react-native` `ViewProps` ,       . [@react-native-community/blur](https://github.com/Kureev/react-native-blur/tree/v4.3.2?tab=readme-ov-file#blurview)  .
 * @param {BlurType} [props.blurType]   . `light`, `dark`, `extraDark`        .
 * @param {number} [props.blurAmount=10]    .      .  `10` `0` `100`   .
 * @param {boolean} [props.vibrancyEffect=false] Vibrancy Effect . Vibrancy          . iOS ,  `false`.
 * @param {string} [props.reducedTransparencyFallbackColor]      .        .
 *
 * @returns {JSX.Element} iOS   `BlurView`  `VibrancyView`  , Android  `View` .
 *
 * ::: warning  
 * `BlurView` iOS . Android  `View` ,    .
 * :::
 *
 * @example
 *
 * ### `BlurView`    
 *
 * ```tsx
 * import { View, Text, StyleSheet } from 'react-native';
 * import { BlurView } from 'react-native-bedrock';
 *
 * export function BlurViewExample() {
 *  return (
 *    <View style={styles.container}>
 *      <Text style={styles.absolute}>Blurred Text</Text>
 *      <BlurView style={styles.absolute} blurType="light" blurAmount={1} />
 *      <Text>Non Blurred Text</Text>
 *    </View>
 *  );
 * }
 *
 * const styles = StyleSheet.create({
 *  container: {
 *    justifyContent: 'center',
 *    alignItems: 'center',
 *    width: '100%',
 *    height: 300,
 *  },
 *  absolute: {
 *    position: 'absolute',
 *    top: 0,
 *    left: 0,
 *    bottom: 0,
 *    right: 0,
 *  },
 * });
 * ```
 *
 * @see [iOS Vibrancy Effect Documentation](https://developer.apple.com/documentation/uikit/uivibrancyeffect)
 * @see [Zeddios Blog ](https://zeddios.tistory.com/1140)
 */

export function BlurView({
  blurType,
  blurAmount = 10,
  reducedTransparencyFallbackColor,
  vibrancyEffect = false,
  ...viewProps
}: BlurViewProps) {
  if (!isBlurNativeModuleSupported || ReactNativeBlurModule == null) {
    return <View {...viewProps} />;
  }

  const Component = vibrancyEffect ? ReactNativeBlurModule.VibrancyView : ReactNativeBlurModule.BlurView;

  return (
    <Component
      blurAmount={blurAmount}
      blurType={blurType}
      reducedTransparencyFallbackColor={reducedTransparencyFallbackColor}
      {...viewProps}
    />
  );
}

BlurView.isSupported = isBlurNativeModuleSupported;
