import { SafeAreaProvider } from '@react-native-bedrock/native/react-native-safe-area-context';
import { ComponentType, PropsWithChildren } from 'react';
import { App } from './App';
import { registerPage } from './registerPage';
import type { InitialProps } from '../initial-props';
import { Router, type RouterProps, type RequireContext } from '../router';
import { BackEventProvider, useBackEventState } from '../use-back-event';

export interface BedrockProps {
  /**
   * @description
   *  .
   */
  appName: string;
  /**
   * @description
   *  .
   *
   * @TODO context 
   */
  context: RequireContext;
  /**
   * @description
   * Bedrock    .
   */
  router?: RouterProps;
}

/**
 * @internal
 */
interface BedrockAppRootProps extends BedrockProps {
  container: ComponentType<PropsWithChildren<InitialProps>>;
  initialProps: InitialProps;
}

function AppRoot({ appName, context, container, initialProps, router }: BedrockAppRootProps) {
  const backEventState = useBackEventState();

  return (
    <App {...initialProps}>
      <SafeAreaProvider>
        <BackEventProvider backEvent={backEventState}>
          <Router
            context={context}
            initialProps={initialProps}
            container={container}
            canGoBack={!backEventState.hasBackEvent}
            onBack={backEventState.onBack}
            // @TODO intoss   scheme   
            prefix={`intoss://${appName}`}
            {...router}
          />
        </BackEventProvider>
      </SafeAreaProvider>
    </App>
  );
}

const createBedrock = () => {
  let _appName: string | null = null;

  return {
    registerApp(
      AppContainer: ComponentType<PropsWithChildren<InitialProps>>,
      { appName, context, router }: BedrockProps
    ): (initialProps: InitialProps) => JSX.Element {
      function Root(initialProps: InitialProps) {
        return (
          <AppRoot
            container={AppContainer}
            initialProps={initialProps}
            appName={appName}
            context={context}
            router={router}
          />
        );
      }

      registerPage(Root);
      _appName = appName;
      return Root;
    },

    get appName(): string {
      if (_appName === null) {
        throw new Error('Bedrock.appName can only be used after registerApp has been called.');
      }
      return _appName;
    },
  };
};

/**
 * @public
 * @category 
 * @name Bedrock
 * @description
 *
 * @property {RegisterService} registerApp -      ,             . `appName`    ,   ,          .
 *
 * `Bedrock.registerApp`     .
 * - :    URL  . Next.js      .   `/my-service/pages/index.ts`  `intoss://my-service`    , `/my-service/pages/home.ts  intoss://my-service/home`    .
 * -  : URL         .  , `referrer`      .
 * -   :      .  ,           .
 * -  (Visibility):   ,     .  ,             .
 *
 * @example
 *
 * ### `Bedrock`   
 *
 * ```tsx
 * import { PropsWithChildren } from 'react';
 * import { Bedrock, InitialProps } from 'react-native-bedrock';
 * import { context } from '../require.context';
 *
 * function AppContainer({ children }: PropsWithChildren<InitialProps>) {
 *  return <>{children}</>;
 * }
 *
 * export default Bedrock.registerApp(AppContainer, {
 *  appName: 'my-app',
 *  context,
 * });
 *
 * ```
 */
export const Bedrock = createBedrock();
