export const hexPattern = /^#([A-Fa-f0-9]{6}|[A-Fa-f0-9]{3})$/;
export const rgbPattern = /^rgb\((\d{1,3}),(\d{1,3}),(\d{1,3})\)$/;
export const rgbaPattern = /^rgba\((\d{1,3}),(\d{1,3}),(\d{1,3}),([\d.]+)\)$/;
export function isHexColor(color) {
    return hexPattern.test(color);
}
export function isRgbColor(color) {
    return rgbPattern.test(color);
}
export function isRgbaColor(color) {
    return rgbaPattern.test(color);
}
export function getColorFormat(color) {
    if (isHexColor(color)) {
        return 'HEX';
    }
    if (isRgbColor(color)) {
        return 'RGB';
    }
    if (isRgbaColor(color)) {
        return 'RGBA';
    }
    return;
}
