import { forwardRef, type ReactElement } from 'react';
import { View, type ViewProps, type ViewStyle } from 'react-native';
import { Children } from './children';
import { Flex } from './flex';
import { Spacing } from './spacing';

type Direction = 'horizontal' | 'vertical';

interface StackProps extends ViewProps {
  direction?: Direction;
  gutter?: number | ReactElement;
  align?: ViewStyle['alignItems'];
  justify?: ViewStyle['justifyContent'];
}

const BaseStack = forwardRef<View, StackProps>(function BaseStack(props, ref) {
  const { direction = 'vertical', gutter = 0, children, ...rest } = props;

  return (
    <Flex ref={ref} direction={direction === 'vertical' ? 'column' : 'row'} {...rest}>
      <Children.Gap
        gap={typeof gutter === 'number' ? <Spacing direction={direction} size={gutter ?? 24} /> : props.gutter}
      >
        {children}
      </Children.Gap>
    </Flex>
  );
});

/**
 * @public
 * @category UI
 * @name Stack
 * @description
 * `Stack`   Stack     ,        .
 * `direction`  (`horizontal`)  (`vertical`)    ,     `gutter`    .
 *    `Stack.Horizontal`,    `Stack.Vertical`    .
 *
 * @param {object} [props] -   props .
 * @param {'flex-start' | 'flex-end' | 'center' | 'stretch' | 'baseline'} [props.align] -  (Flex )     . `'column'`    `'center'`   , `'stretch'`   `'auto'`     .   [`alignItems`](https://reactnative.dev/docs/0.72/layout-props#alignitems) .
 * @param {'flex-start' | 'flex-end' | 'center' | 'space-between' | 'space-around' | 'space-evenly'} [props.justify] - (Flex   )      . `'column'`   , `flex-start`    , `'center'`    .   [`justifyContent`](https://reactnative.dev/docs/0.72/layout-props#justifycontent) .
 * @param {'vertical' | 'horizontal'} [props.direction = 'vertical'] -      .  `'vertical'`.
 * @param {number | ReactElement} [props.gutter] -      .      , `ReactElement`     .       , `ReactElement`        .
 *
 * @property {StackHorizontal} [Horizontal] - `Stack.Horizontal`   ** **   .
 * @property {StackVertical} [Vertical] - `Stack.Vertical`   ** **   .
 *
 * @example
 * ### ,      16  .
 *
 * ```tsx
 * import { Text } from 'react-native';
 * import { Stack } from '@react-native-bedrock/core';
 *
 * export function StackExample() {
 *   return (
 *     <>
 *       <Stack gutter={16} direction="horizontal">
 *         <Text>16    </Text>
 *         <Text>1</Text>
 *         <Text>2</Text>
 *         <Text>3</Text>
 *       </Stack>
 *       <Stack gutter={16} direction="vertical">
 *         <Text>16    </Text>
 *         <Text>1</Text>
 *         <Text>2</Text>
 *         <Text>3</Text>
 *       </Stack>
 *     </>
 *   );
 * }
 * ```
 */
export const Stack = BaseStack as StackType;

type StackType = typeof BaseStack & {
  Vertical: typeof StackVertical;
  Horizontal: typeof StackHorizontal;
};

type StackWithoutDirectionProps = Omit<StackProps, 'direction'>;

/**
 * @public
 * @category UI
 * @name StackHorizontal
 * @description
 * `Stack.Horizontal`      .   ,     `gutter`     ,       .
 *
 * @param {object} [props] -   props .
 * @param {'flex-start' | 'flex-end' | 'center' | 'stretch' | 'baseline'} [props.align = 'stretch'] -      . Flexbox `align-items`   ,  `'stretch'`    `'auto'`     .
 * @param {'flex-start' | 'flex-end' | 'center' | 'space-between' | 'space-around' | 'space-evenly'} [props.justify = 'flex-start'] -      . Flexbox `justify-content`   ,  `'flex-start'`    .
 * @param {number | ReactElement} [props.gutter] -      .      , `ReactElement`     .       , `ReactElement`        .
 *
 * @example
 * ###    ,  16  .
 *
 * ```tsx
 * import { Stack } from '@react-native-bedrock/core';
 * import { View, Text } from 'react-native';
 *
 * function StackHorizontalExample() {
 *   return (
       <Stack.Horizontal gutter={16}>
        <Text>16    </Text>
        <Text>1</Text>
        <Text>2</Text>
        <Text>3</Text>
      </Stack.Horizontal>
 *   );
 * }
 * ```
 */
const StackHorizontal = forwardRef<View, StackWithoutDirectionProps>(function StackHorizontal(props, ref) {
  return <Stack direction="horizontal" {...props} ref={ref} />;
});

/**
 * @public
 * @category UI
 * @name StackVertical
 * @description
 * `Stack.Vertical`      .        `gutter`     ,       .
 *
 * @param {object} [props] -   props .
 * @param {string} [props.align = 'stretch'] -      . Flexbox `align-items`   ,  `'stretch'`    `'auto'`     .
 * @param {string} [props.justify = 'flex-start'] -      . Flexbox `justify-content`   ,  `'flex-start'`    .
 * @param {number | ReactElement} [props.gutter] -      .      , `ReactElement`     .       , `ReactElement`        .
 *
 * @example
 * ###      16  .
 *
 * ```tsx
 * import { Stack } from '@react-native-bedrock/core';
 * import { View, Text } from 'react-native';
 *
 * ```tsx
 * import { Stack } from '@react-native-bedrock/core';
 * import { View, Text } from 'react-native';
 *
 * function StackVerticalExample() {
 *   return (
       <Stack.Vertical gutter={16}>
        <Text>16    </Text>
        <Text>1</Text>
        <Text>2</Text>
        <Text>3</Text>
      </Stack.Vertical>
 *   );
 * }
 * ```
 */
const StackVertical = forwardRef<View, StackWithoutDirectionProps>(function StackVertical(props, ref) {
  return <Stack direction="vertical" {...props} ref={ref} />;
});

Stack.Horizontal = StackHorizontal;
Stack.Vertical = StackVertical;
