import FastImage, {
  FastImageProps,
  Source as FastImageSource,
} from '@react-native-bedrock/native/react-native-fast-image';
import { StyleSheet } from 'react-native';
import { SvgImage } from './SvgImage';

type Source = {
  uri?: string;
  cache?: FastImageSource['cache'];
};

interface ImageProps extends Omit<FastImageProps, 'source'> {
  source?: Source;
}

/**
 * @public
 * @category UI
 * @name Image
 * @description `Image`    (png, jpg )  (svg)     .       .
 * @link https://github.com/DylanVann/react-native-fast-image/tree/v8.6.3/README.md
 *
 * @param {object} [props] -   `props` .
 * @param {object} [props.style] -     . `width`, `height`       .
 * @param {object} [props.source] -        .
 * @param {string} [props.source.uri] -     URI .
 * @param {'immutable' | 'web' | 'cacheOnly'} [props.source.cache = 'immutable'] -       .     .  `immutable`.
 * @param {() => void} [props.onLoadStart] -       .
 * @param {() => void} [props.onLoadEnd] -       .
 * @param {() => void} [props.onError] -         .
 *
 * @example
 * ###     
 *
 *        ,    `console.log`     .
 *
 * ```tsx
 * import { View } from 'react-native';
 * import { Image } from 'react-native-bedrock';
 *
 * export function ImageExample() {
 *   return (
 *     <View>
 *       <Image
 *         source={{ uri: 'my-image-link' }}
 *         style={{
 *           width: 300,
 *           height: 300,
 *           borderWidth: 1,
 *         }}
 *         onError={() => {
 *           console.log('  ');
 *         }}
 *       />
 *
 *       <Image
 *         source={{ uri: 'my-svg-link' }}
 *         style={{
 *           width: 300,
 *           height: 300,
 *           borderWidth: 1,
 *         }}
 *         onError={() => {
 *           console.log('  ');
 *         }}
 *       />
 *     </View>
 *   );
 * }
 * ```
 */
function Image(props: ImageProps) {
  if (typeof props.source === 'object' && props.source.uri?.endsWith('.svg')) {
    const style = StyleSheet.flatten(props.style);
    const width = style?.width;
    const height = style?.height;

    return (
      <SvgImage
        testID={props.testID}
        url={props.source.uri!}
        width={width}
        height={height}
        style={props.style}
        onLoadStart={props.onLoadStart}
        onLoadEnd={props.onLoadEnd}
        onError={props.onError}
      />
    );
  }

  return <FastImage {...props} />;
}

export { Image };
