export const MAX_ELEVATION = 145;
export const ELEVATION_PROPOTION = {
    0: 0,
    3: 1,
    30: 50,
    50: 125,
    60: 130,
    80: MAX_ELEVATION,
};
/**
 * @description shadow radius   elevation  .
 */
export const calculateElevation = (radius) => {
    const keys = Object.keys(ELEVATION_PROPOTION).map(Number);
    let index = keys.findIndex(key => key > radius);
    if (index === -1) {
        index = keys.length - 1;
    }
    if (index === 0) {
        return 0;
    }
    const start = keys[index - 1];
    const end = keys[index];
    const startValue = ELEVATION_PROPOTION[start];
    const endValue = ELEVATION_PROPOTION[end];
    if (startValue === undefined || endValue === undefined) {
        return 0;
    }
    const calculatedValue = startValue + ((radius - start) / (end - start)) * (endValue - startValue);
    return calculatedValue > MAX_ELEVATION ? MAX_ELEVATION : calculatedValue;
};
