/**
 *
 * @description rgba   array .
 */
export function extractNumbersFromRGBA(rgbaString) {
    const match = rgbaString.match(/rgba\(([\d.,\s]+)\)/);
    if (!match) {
        return [0, 0, 0, 0];
    }
    const numbersString = match[1];
    const numbersArray = numbersString.split(',').map(s => parseFloat(s.trim()));
    return numbersArray;
}
