import { Animated } from 'react-native';
import { getSpringEasing } from '@toss-design-system/spring-easing';
/**
 *
 * `@toss-design-system/easings` `spring`     .
 *
 * @example
 * ```ts
 * import { spring } from '@toss-design-system/easings';
 * import { Animated } from '';
 *
 * const value = new Animated.Value(0);
 * Animated.spring(value, {
 *  toValue: 1,
 *  useNativeDriver: false,
 *  ...spring.basic,
 * }).start();
 * ```
 */
export const spring = function (value, config) {
    const { stiffness, damping, velocity, tolerance, mass, ...restConfig } = config;
    const { duration, easeFunction } = getSpringEasing({
        stiffness,
        damping,
        velocity,
        tolerance,
        mass,
    });
    return Animated.timing(value, {
        ...restConfig,
        easing: easeFunction,
        duration: duration * 1000,
    });
};
