/**
 * URL     .
 * @param params -   
 * @returns   
 * @example
 * // : { foo: '123', bar: 'true' }
 * // : { foo: 123, bar: true }
 */
export function defaultParserParams(params?: Record<string, string>): Record<string, unknown> {
  return Object.fromEntries(
    Object.entries(params ?? {}).map(([key, value]) => {
      try {
        return [key, JSON.parse(value)];
      } catch {
        return [key, value];
      }
    })
  );
}
