/**
 *
 * @description Create a selector from a class name object
 * @example
 * const ClassNames = {
 *  Text: 'text',
 *  Icon: 'icon',
 * }
 *
 * export const ClassNameSelectors = createSelectorFromClassNames(ClassNames);
 * ClassNameSelectors.Text // .text
 * ClassNameSelectors.Icon // .icon
 */
export function createSelectorFromClassNames(obj) {
    return Object.entries(obj).reduce((acc, [key, value]) => ({
        ...acc,
        [key]: `.${value}`,
    }), {});
}
