const CENTER_PADDING_TOP_FALLBACK = 0;
export function getCenterPaddingTop(left, center) {
    if (left == null || center == null) {
        return CENTER_PADDING_TOP_FALLBACK;
    }
    if (left.type !== 'numberIcon' && center.hasDescription) {
        return 0;
    }
    if (left.type === 'asset32' || left.type === 'numberIcon') {
        return {
            A: 5,
            B: 2,
            C: 5,
        }[center.type];
    }
    if (left.type === 'asset40') {
        return {
            A: 9,
            B: 6,
            C: 9,
        }[center.type];
    }
    return CENTER_PADDING_TOP_FALLBACK;
}
const RIGHT_PADDING_TOP_FALLBACK = 0;
export function getRightPaddingTop(left, right) {
    if (left == null || right == null) {
        return RIGHT_PADDING_TOP_FALLBACK;
    }
    if (left.type === 'asset32' || left.type === 'numberIcon') {
        return {
            arrow: 4,
            button: 0,
            none: 0,
        }[right.type];
    }
    if (left.type === 'asset40') {
        return {
            arrow: 8,
            button: 4,
            none: 0,
        }[right.type];
    }
    return RIGHT_PADDING_TOP_FALLBACK;
}
