import {
  createNavigationContainerRef,
  NavigationContainer,
  NavigationContainerRefWithCurrent,
  ParamListBase,
  RouteProp,
} from '@react-native-bedrock/native/@react-navigation/native';
import { NativeStackNavigationOptions } from '@react-native-bedrock/native/@react-navigation/native-stack';
import { ComponentProps, ComponentType, Fragment, PropsWithChildren, ReactElement, useCallback, useMemo } from 'react';
import { BedrockStack } from './components/BedrockStack';
import { CanGoBackGuard } from './components/CanGoBackGuard';
import { RouterBackButton, RouterBackButtonProps } from './components/RouterBackButton';
import { useBedrockRouterControls } from './hooks/useBedrockRouterControls';
import { useInitialRouteName } from './hooks/useInitialRouteName';
import { RequireContext } from './types';
import { BASE_STACK_NAVIGATOR_STYLE } from './types/screen-option';
import { InitialProps } from '../initial-props';

/**
 * @internal
 */
export interface InternalRouterProps {
  /**
   * @name context
   * @description
   *        .
   */
  context: RequireContext;
  /**
   * @name prefix
   * @description
   *     prefix .  , `intoss://my-service/intro`    `intoss://my-service` prefix  .
   */
  prefix: string;
  /**
   * @name canGoBack
   * @description
   *     .  `true`, `true`   `@react-navigation/native`         .
   * @default true
   */
  canGoBack?: boolean;
  /**
   * @name onBack
   * @description
   *      .
   */
  onBack?: () => void;
  /**
   * @name container
   * @description
   * `@react-navigation/native` `Navigator`   .
   */
  container: ComponentType<PropsWithChildren<InitialProps>>;
  initialProps: InitialProps;
}

export type RouterProps = StackNavigatorProps & NavigationContainerProps;

interface StackNavigatorProps {
  /**
   * @name navigationContainerRef
   * @description
   * `@react-navigation/native` `NavigationContainerRef`     .       .
   */
  navigationContainerRef?: NavigationContainerRefWithCurrent<any>;
  /**
   * @name defaultScreenOption
   * @description
   *    .       .  , `title` `headerStyle`    .
   */
  defaultScreenOption?:
    | NativeStackNavigationOptions
    | ((props: { route: RouteProp<ParamListBase>; navigation: any }) => NativeStackNavigationOptions);
  /**
   * @name screenContainer
   * @description
   *  `Screen`    .
   */
  screenContainer?: ComponentType<PropsWithChildren<any>>;
}

type NavigationContainerProps = Pick<
  ComponentProps<typeof NavigationContainer>,
  'ref' | 'documentTitle' | 'fallback' | 'onReady' | 'onUnhandledAction' | 'onStateChange'
>;

/**
 * @category Components
 * @kind function
 * @name Router
 * @description
 * React Native      .
 * `pages/*`         .
 *    Next.js          .
 *
 * @param {string} prefix     prefix .  , `intoss://my-service/intro`   `intoss://my-service` prefix  .
 * @param {RequireContext} context         .
 * @param {NavigationContainerRefWithCurrent<any>} [navigationContainerRef] `@react-navigation/native` `NavigationContainerRef`     .       .
 * @param {NativeStackNavigationOptions | ((props: { route: RouteProp<ParamListBase>; navigation: any }) => NativeStackNavigationOptions)} [defaultScreenOption]    .       .  , title  headerStyle    .
 * @param {boolean} [canGoBack=true]     .  `true`, `true`   `@react-navigation/native`          .
 * @param {() => void} [onBack]           .  ,            .
 * @param {ComponentType<{ children: ReactNode }>} [container=Fragment] `@react-navigation/native` `Navigator`   .
 * @param {NavigationContainerProps} [navigationContainerProps] - `@react-navigation/native` `NavigationContainer`  props   .
 *
 * @returns {ReactElement} -   .
 * @example
 * ```tsx
 * import { Router } from 'react-native-bedrock';
 * import { context } from '../require.context';
 *
 * function App() {
 *  return <Router context={context} prefix={'intoss://testbench'} />;
 * }
 * ```
 */
export function Router({
  // Internal props
  prefix,
  context,
  canGoBack = true,
  onBack,
  container: BedrockContainer = Fragment,
  initialProps,
  // Public props (NavigationContainer)
  navigationContainerRef,
  defaultScreenOption,
  screenContainer,
  // Public props (StackNavigator)
  ...navigationContainerProps
}: InternalRouterProps & RouterProps): ReactElement {
  const initialRouteName = useInitialRouteName(prefix);
  const { BedrockScreens, linkingOptions } = useBedrockRouterControls({ prefix, context, screenContainer });

  const ref = useMemo(() => navigationContainerRef ?? createNavigationContainerRef<any>(), [navigationContainerRef]);

  const headerLeft = useCallback(
    (backButtonProps: Omit<RouterBackButtonProps, 'navigationContainerRef'>) => (
      <RouterBackButton {...backButtonProps} onBack={onBack} canGoBack={canGoBack} navigationContainerRef={ref} />
    ),
    [onBack, canGoBack, ref]
  );

  const screenOptions = useCallback(
    (screenProps: any) => ({
      ...BASE_STACK_NAVIGATOR_STYLE,
      gestureEnabled: canGoBack,
      headerLeft,
      ...(typeof defaultScreenOption === 'function' ? defaultScreenOption(screenProps) : defaultScreenOption),
    }),
    [canGoBack, defaultScreenOption, headerLeft]
  );

  return (
    <NavigationContainer ref={ref} {...navigationContainerProps} linking={linkingOptions}>
      <CanGoBackGuard canGoBack={canGoBack} onBack={onBack}>
        <BedrockContainer {...initialProps}>
          <BedrockStack.Navigator initialRouteName={initialRouteName} screenOptions={screenOptions}>
            {BedrockScreens}
          </BedrockStack.Navigator>
        </BedrockContainer>
      </CanGoBackGuard>
    </NavigationContainer>
  );
}
