/* eslint-disable import/no-duplicates */
import type { VideoProperties } from '@react-native-bedrock/native/react-native-video';
import type VideoRef from '@react-native-bedrock/native/react-native-video';
import { forwardRef, Ref, useMemo, useState } from 'react';
import { Animated, Platform } from 'react-native';
import { Video as RNVideo } from './instance';
import { isAvailable } from './instance';
import { useVisibility } from '../visibility';

const AnimatedRNVideo = Animated.createAnimatedComponent(RNVideo);

// Add this interface declaration
interface VideoComponent extends React.ForwardRefExoticComponent<VideoProperties & React.RefAttributes<VideoRef>> {
  isAvailable: boolean;
}

/**
 * @public
 * @category UI
 * @name OnAudioFocusChanged
 * @description       . `muted`  `false`    .
 *
 * @param {Object} event -       .
 * @param {boolean} event.hasAudioFocus -        .
 */
type OnAudioFocusChanged = NonNullable<VideoProperties['onAudioFocusChanged']>;

type Props =
  | (VideoProperties & { muted: true })
  | (VideoProperties & { muted: false; onAudioFocusChanged: OnAudioFocusChanged });

/**
 * @public
 * @category UI
 * @name RNVideoRef
 * @description
 * `react-native-video` ref .
 */
export type RNVideoRef = VideoRef;

/**
 * @public
 * @name Video
 * @category UI
 * @description
 * Video       ,            .      .  ,      .
 *
 * ::: warning
 * Video  [`react-native-video` (6.0.0-alpha.6)](https://github.com/TheWidlarzGroup/react-native-video/tree/v6.0.0-alpha.6)   .          .
 * :::
 *
 * @property {boolean} [isAvailable] `Video`      .           (:   ,    )          .   `false`,          .
 *
 * @param {VideoProperties} [props] [`react-native-video`](https://github.com/TheWidlarzGroup/react-native-video/tree/v6.0.0-alpha.6)  .
 * @param {string} [props.source.uri]   .   URL   .
 * @param {boolean} [props.muted=false]    . `true`   , `false`  .  `false`.
 * @param {boolean} [props.paused=false]    . `true`   , `false`  .  `false`,  .
 * @param {OnAudioFocusChanged} [props.onAudioFocusChanged]       . `muted`  `false`    .   [OnAudioFocusChanged](/react-native/reference/react-native-bedrock/Types/OnAudioFocusChanged.html) .
 * @param {Ref<VideoRef>} ref     ref .  ref        .
 *
 * @returns {JSX.Element}   JSX  . `Animated`        .
 *
 * @see [react-native-video] https://github.com/react-native-video/react-native-video
 *       .
 * @see [react-native-video-6.0.0-alpha.6] https://github.com/TheWidlarzGroup/react-native-video/releases/tag/v6.0.0-alpha.6
 *     .
 *
 * @example
 *
 * ###   
 *
 * ```tsx
 * import { useRef } from 'react';
 * import { View } from 'react-native';
 * import { Video } from 'react-native-bedrock';
 *
 * export function VideoExample() {
 *   const videoRef = useRef(null);
 *
 *   return (
 *     <View>
 *       <Video
 *         ref={videoRef}
 *         source={{ uri: 'https://commondatastorage.googleapis.com/gtv-videos-bucket/sample/BigBuckBunny.mp4' }}
 *         muted={true}
 *         paused={false}
 *         resizeMode="cover"
 *         style={{ width: 300, height: 200, borderWidth: 1 }}
 *       />
 *     </View>
 *   );
 * }
 * ```
 */
export const Video = forwardRef((props: Props, ref: Ref<VideoRef>) => {
  const [isFocused, setIsFocused] = useState(props.muted || props.paused);
  const visible = useVisibility();

  // onAudioFocusChanged       ,    paused  .
  const paused = useMemo(
    () => !visible || props.paused || (!props.onAudioFocusChanged && !isFocused),
    [props.onAudioFocusChanged, props.paused, visible, isFocused]
  );

  const disableFocus = props.muted || props.paused;
  const mixWithOthers = props.muted ? 'mix' : undefined;

  return (
    <AnimatedRNVideo
      ref={ref}
      /**
       * @name enableDecoderFallback
       * @description
       *   16       ,
       *      .
       *    fallback     prop    .
       *  false        .
       */
      progressUpdateInterval={16}
      disableFocus={Platform.OS === 'ios' ? false : disableFocus}
      playWhenInactive
      onAudioFocusChanged={({ hasAudioFocus }) => setIsFocused(hasAudioFocus)}
      {...props}
      enableDecoderFallback={true}
      paused={paused}
      mixWithOthers={mixWithOthers}
    />
  );
});

(Video as VideoComponent).isAvailable = isAvailable;
