import { Platform } from 'react-native';

/**
 * @public
 * @kind function
 * @category  
 *
 * @name getPlatformOS
 * @signature
 * ```typescript
 * function getPlatformOS(): 'ios' | 'android';
 * ```
 *
 * @description
 *      .
 *   `react-native` [`Platform.OS`](https://reactnative.dev/docs/0.72/platform#os)   , `ios`  `android`    .
 *
 * @returns {'ios' | 'android'}    
 *
 * @example
 *
 * ###   OS  
 *
 * ```tsx
 * import { getPlatformOS } from 'react-native-bedrock';
 * import { Text } 'react-native';
 *
 * function Page() {
 *   const platform = getPlatformOS();
 *
 *   return <Text> : {platform}</Text>;
 * }
 * ```
 */
export function getPlatformOS(): 'ios' | 'android' {
  return Platform.OS as 'ios' | 'android';
}
