import { BedrockModule } from './BedrockModule';

/**
 * @public
 * @name getSchemeUri
 * @category  
 * @kind function
 * @description     .    URI   .
 * @returns {string}      .
 *
 * @example
 * ###     
 *
 * ```tsx
 * import { getSchemeUri } from 'react-native-bedrock';
 * import { Text } from 'react-native';
 *
 * function MyPage() {
 *  const schemeUri = getSchemeUri();
 *
 *  return <Text>    : {schemeUri}</Text>
 * }
 * ```
 */
export function getSchemeUri() {
  return BedrockModule.schemeUri;
}
