import { useCallback, useMemo, useRef, useState } from 'react';
import { Animated } from '../../../interactions/animated';
import { spring } from '@toss-design-system/easings';
import { sToMs } from './sToMs';
import { BlinkAnimationState } from './animationState';
export const useBlinkAnimation = ({ containerSize }) => {
    const containerScale = useRef(new Animated.Value(1)).current;
    const dimmerOpacity = useRef(new Animated.Value(0)).current;
    const gradientOpacity = useRef(new Animated.Value(0)).current;
    const gradientScale = useRef(new Animated.Value(0)).current;
    const [animationState, setAnimationState] = useState(BlinkAnimationState.Idle);
    const start = useCallback(() => {
        setAnimationState(BlinkAnimationState.StartAnimation | BlinkAnimationState.Playing);
        containerScale.setValue(1);
        const containerAnimation = Animated.sequence([
            Animated.spring(containerScale, {
                toValue: 1.02,
                stiffness: 766,
                damping: 52,
                useNativeDriver: true,
            }),
            Animated.spring(containerScale, {
                toValue: 1,
                ...spring.slow,
                useNativeDriver: true,
            }),
        ]);
        gradientOpacity.setValue(0);
        const dimmerAnimation = Animated.parallel([
            Animated.spring(dimmerOpacity, {
                toValue: 0.04,
                delay: sToMs(0.48),
                ...spring.basic,
                useNativeDriver: true,
            }),
        ]);
        gradientScale.setValue(0);
        gradientOpacity.setValue(0);
        const gradientAnimation = Animated.parallel([
            Animated.spring(gradientOpacity, {
                toValue: 1,
                delay: sToMs(0.02),
                ...spring.basic,
                useNativeDriver: true,
            }),
            Animated.spring(gradientScale, {
                toValue: 1,
                delay: sToMs(0.02),
                ...spring.slow,
                useNativeDriver: true,
            }),
        ]);
        Animated.parallel([containerAnimation, dimmerAnimation, gradientAnimation]).start(() => {
            setAnimationState(BlinkAnimationState.StartAnimation | BlinkAnimationState.Finished);
        });
    }, [containerScale, dimmerOpacity, gradientOpacity, gradientScale]);
    const exit = useCallback(() => {
        setAnimationState(BlinkAnimationState.EndAnimation | BlinkAnimationState.Playing);
        const containerAnimation = Animated.spring(containerScale, {
            toValue: 1,
            ...spring.quick,
            useNativeDriver: true,
        });
        const dimmerAnimation = Animated.spring(dimmerOpacity, {
            toValue: 0,
            ...spring.slow,
            useNativeDriver: true,
        });
        const gradientAnimation = Animated.spring(gradientOpacity, {
            toValue: 0,
            ...spring.slow,
            useNativeDriver: true,
        });
        Animated.parallel([containerAnimation, dimmerAnimation, gradientAnimation]).start(() => {
            setAnimationState(BlinkAnimationState.EndAnimation | BlinkAnimationState.Finished);
        });
    }, [containerScale, dimmerOpacity, gradientOpacity]);
    const animation = useMemo(() => ({
        container: {
            transform: [{ scale: containerScale }],
        },
        dimmer: {
            opacity: dimmerOpacity,
        },
        gradient: {
            opacity: gradientOpacity,
            transform: [
                { scale: gradientScale },
                { translateY: Animated.divide(Animated.multiply(gradientScale, -containerSize.height), 2) },
            ],
        },
        state: animationState,
        start,
        exit,
    }), [containerScale, dimmerOpacity, animationState, exit, containerSize, gradientOpacity, gradientScale, start]);
    return animation;
};
