import { shouldUseNativeDriver } from '../helper';
import { Animation } from './animations';
import { createMotionPropertyAnimation } from './createMotionPropertyAnimation';
/*
 *   , parallel   animation  .
 */
export function createMotionAnimation({ progress, toValue, motion, }) {
    const useNativeDriver = Object.keys(motion).every(key => shouldUseNativeDriver(key));
    const animations = Object.values(motion).map(value => {
        return createMotionPropertyAnimation({ progress, toValue, useNativeDriver, ...value });
    });
    return Animation.parallel(animations);
}
