import { Platform } from 'react-native';
import { calculateElevation } from './calculateElevation';
/**
 * @description   shadow style .
 * @returns iOS: { shadowColor, shadowOffset, shadowOpacity, shadowRadius }, Android: { shadowColor, elevation }
 */
export const generateShadowStyle = ({ offset = { x: 0, y: 0 }, opacity = 1, radius, colorPreference = 'light', ...colorProps }) => {
    const shadowColor = 'color' in colorProps
        ? colorProps.color
        : colorPreference === 'light'
            ? colorProps.lightColor
            : colorProps.darkColor;
    return Platform.OS === 'ios'
        ? {
            shadowColor,
            shadowOffset: { width: offset.x ?? 0, height: offset.y ?? 0 },
            shadowOpacity: opacity,
            shadowRadius: radius,
        }
        : { shadowColor, elevation: calculateElevation(radius) };
};
