import {
  Button,
  FixedBottomCTA,
  useToast,
} from "@toss-design-system/react-native";
import { useNavigation } from "react-native-bedrock";
import { routeStack } from "../utill/route-stack";
import { useAppDispatch, useAppSelector } from "store";
import { cityViewList } from "../utill/city-list";
import { useTendencyHandler } from "../hooks/useTendencyHandler";
import { getRegionInfo } from "../redux/travle-slice";

export const RouteButton = ({
  disabled,
  onPress,
}: {
  disabled?: boolean;
  onPress?: () => void;
}) => {
  const navigation = useNavigation();
  const { region, country, cityIndex } = useAppSelector(
    (state) => state.travelSlice
  );
  const dispatch = useAppDispatch();
  const handleBack = () => {
    if (navigation.canGoBack()) {
      navigation.goBack();
    }
  };

  const { countryList } = useTendencyHandler();
  const { open } = useToast();
  const goNext = () => {
    if (region.length == 0) {
      open(" .", {
        icon: "icon-warning-circle",
      });
      return false;
    } else {
      //==  ,  
      const city = cityViewList[country][cityIndex];
      const isDomestic = country == 0; // 
      const regionName = region[0]; // 
      const subTitle = city?.sub?.[1]?.subTitle ?? "";
      const cityEng = city.eng ?? ""; //    
      const countryEn = countryList[country].en; ///japan   
      //  -   +  ex) [' '] == ' '
      //  - ' '
      //  -     normalize  .
      // -     cityEng   +      .
      //  = ' ' ....   = '/japan/ (Kanto) ! or /Philippines/  !
      const data = isDomestic
        ? city.title === ""
          ? `${regionName} `
          : city.title === ""
          ? " "
          : `${city.title} ${regionName !== "" ? regionName : subTitle}`
        : `/${countryEn}/${city.title.normalize("NFD")} ${cityEng.normalize(
            "NFD"
          )}${cityEng ? " " : ""}${
            regionName !== "" ? "!" + regionName : "!" + subTitle
          }`;

      //regionINfo api
      dispatch(
        getRegionInfo({
          region: data,
        })
      );
      return true;
    }
  };

  const handleNext = () => {
    if (onPress) {
      onPress();
    }
    const nowStep = navigation
      .getState()
      ?.routes?.at(-1)
      ?.name.split("/enroll")[1];
    if (nowStep == "/region") {
      const shouldProceed = goNext();
      if (shouldProceed) {
        navigation.navigate(
          (nowStep == "/distance" ? "" : "/enroll") + routeStack[nowStep]?.next
        );
      }
    } else if (nowStep == "/country") {
      if (country == null) {
        open(" .", {
          icon: "icon-warning-circle",
        });
      } else {
        navigation.navigate(
          (nowStep == "/distance" ? "" : "/enroll") + routeStack[nowStep]?.next
        );
      }
    } else {
      navigation.navigate(
        (nowStep == "/distance" ? "" : "/enroll") + routeStack[nowStep]?.next
      );
    }
  };
  return (
    <FixedBottomCTA.Double
      leftButton={
        <Button type="dark" style="weak" display="block" onPress={handleBack}>
          
        </Button>
      }
      rightButton={
        <Button
          display="block"
          onPress={handleNext}
          disabled={disabled ?? false}
        >
          
        </Button>
      }
    />
  );
};
