/**
 *
 *        .
 *
 * @example
 * const target = { asd: 1, aa: 1, ab: 2, b: 3, avery: 4 };
 * const keys = ['a'];
 * const filtered = filterByKeys(target, keys);
 * console.log(filtered); // { asd: 1, aa: 1, ab: 2, avery: 4 }
 *
 */
export function filterByKeys(target, keys) {
    const result = {};
    Object.keys(target).forEach(key => {
        const keyStr = key;
        if (keys.some(substring => keyStr.includes(substring))) {
            result[key] = target[key];
        }
    });
    return result;
}
