import { Platform } from 'react-native';
import { BedrockModule } from './BedrockModule';

/**
 * @public
 * @category 
 * @kind function
 * @name getLocale
 * @description
 *  (locale)  .          'ko-KR' .          .
 *
 * @returns {string}    .
 *
 * @example
 * ###     
 *
 * ```tsx
 * import { getLocale } from 'react-native-bedrock';
 * import { Text } from 'react-native';
 *
 * function MyPage() {
 *  const locale = getLocale();
 *
 *  return (
 *    <Text>  : {locale}</Text>
 *  )
 * }
 *
 * ```
 */
export function getLocale() {
  const locale = BedrockModule?.DeviceInfo?.locale ?? 'ko-KR';

  if (Platform.OS === 'android') {
    return replaceUnderbarToHypen(locale);
  }

  return locale;
}

/**
 * android  (`_`)   ,  (`-`) .
 */
function replaceUnderbarToHypen(locale: string) {
  return locale.replace(/_/g, '-');
}
