import { useState } from 'react';
export const useTextLine = () => {
    const [textLine, setTextLine] = useState('single');
    const updateTextLine = ({ nativeEvent: { lines } }) => {
        if (lines.length >= 2) {
            setTextLine('multiple');
        }
        else {
            setTextLine('single');
        }
    };
    return {
        textLine,
        updateTextLine,
    };
};
