import { FlatList } from "@react-native-bedrock/native/react-native-gesture-handler";
import {
  BottomCTA,
  BottomSheet,
  Button,
  colors,
  FixedBottomCTA,
  FixedBottomCTAProvider,
  ListRow,
  StepperRow,
  Tab,
  Text,
  useBottomSheet,
  useToast,
} from "@toss-design-system/react-native";
import moment from "moment";
import React, { useCallback, useRef, useState } from "react";
import { Dimensions, View } from "react-native";
import { BedrockRoute, Stack, useNavigation } from "react-native-bedrock";
import { useAppDispatch, useAppSelector } from "store";
import CustomMapView from "../components/map-view";
import CustomMapViewMarker from "../components/map-view-marker";
import {
  detailTripadvisor,
  recommendApi,
  recommendTripadvisor,
  travelSliceActions,
} from "../redux/travle-slice";
import { useDistance } from "../hooks/useDistance";
import { CommonActions } from "@react-native-bedrock/native/@react-navigation/native";
import NavigationBar from "../components/navigation-bar";

export const Route = BedrockRoute("/preset-detail", {
  validateParams: (params) => params,
  component: PresetDetail,
});

function PresetDetail() {
  const params = Route.useParams();
  const [tabValue, setTabalue] = useState("0");
  const { presetDatas, presetTendencyList, nDay, day, region } = useAppSelector(
    (state) => state.travelSlice
  );
  const scrollRef = useRef(null);
  const weekdays = ["", "", "", "", "", "", ""];
  const dispatch = useAppDispatch();
  const bottomSheet = useBottomSheet();
  const navigation = useNavigation();
  const { open } = useToast();

  const accommodationRecommend = async (e: {
    value: any;
    index: number;
    idx: number;
  }) => {
    try {
      let timetable = presetDatas[params?.index];
      if (timetable[e.idx].length < 2) {
      } else {
        let lat = 0;
        let lng = 0;
        let goCheck = true;
        if (e.index == 0) {
          if (timetable[e.idx][e.index + 1].name.includes("")) {
            goCheck = false;
          } else {
            lat = timetable[e.idx][e.index + 1].lat;
            lng = timetable[e.idx][e.index + 1].lng;
          }
        } else {
          if (timetable[e.idx][e.index - 1].name.includes("")) {
            goCheck = false;
          } else {
            lat = timetable[e.idx][e.index - 1].lat;
            lng = timetable[e.idx][e.index - 1].lng;
          }
        }
        if (goCheck) {
          const startNumber = e.value.y; //  
          const count = e.value.takenTime / 30; //  
          const sequentialArray = Array.from(
            { length: count },
            (_, index) => startNumber + index
          );
          const data = await getRecommendList({
            name: " ",
            x: e.value.x,
            index: e.index,
            y: sequentialArray,
            category: e.value.category,
            lat: lat,
            lng: lng,
            apiCategory: region[0].startsWith("") ? "hotels" : "AD5",
            radius: 2000,
            backupLat:
              e.index != 0
                ? timetable[e.idx][e.index - 1].lat
                : timetable[e.idx][e.index + 1].lat,
            backupLng:
              e.index != 0
                ? timetable[e.idx][e.index - 1].lng
                : timetable[e.idx][e.index + 1].lng,
            status:
              e.index != 0
                ? timetable[e.idx][e.index - 1]
                : timetable[e.idx][e.index + 1],
          });
          return data;
        } else {
          // dispatch(
          // 	modalSliceActions.setOpenModal({
          // 		modalTitle: ' .',
          // 		modalSubTitle: ',    \n   ',
          // 	}),
          // );
        }
      }
    } catch (e) {}
  };

  const getRecommendList = async (e: {
    name: string;
    x: number;
    index: number;
    y: number;
    category: string;
    lat: number;
    lng: number;
    apiCategory: string;
    radius: number;
    backupLat: number;
    backupLng: number;
    status: any;
  }) => {
    try {
      let result = await dispatch(
        region[0].startsWith("")
          ? recommendTripadvisor({
              category: e.apiCategory,
              lat: e.lat,
              lng: e.lng,
              radius: e.radius,
              name: e.status.name,
            })
          : recommendApi({
              category: e.apiCategory,
              lat: e.lat,
              lng: e.lng,
              radius: e.radius,
            })
      ).unwrap();
      result = region[0].startsWith("") ? result.data : result;
      if (result.length == 0) {
        result = await dispatch(
          region[0].startsWith("")
            ? recommendTripadvisor({
                category: e.apiCategory,
                lat: e.lat,
                lng: e.lng,
                radius: Number(e.radius) * 1.5,
                name: e.status.name,
              })
            : recommendApi({
                category: e.apiCategory,
                lat: e.lat,
                lng: e.lng,
                radius: Number(e.radius) * 1.5,
              })
        ).unwrap();
        result = region[0].startsWith("") ? result.data : result;
        result.length == 0 && open("      ");
        return [];
      }
      if (region[0].startsWith("")) {
        let copy = await dispatch(
          detailTripadvisor({ id: result[0].location_id })
        ).unwrap();
        console.log(copy);
        result = [
          {
            ...result,
            place_name: copy.name,
            y: copy.latitude,
            x: copy.longitude,
          },
          {},
        ];
      }
      return result;
    } catch (err) {
      open("      ");
      navigation.goBack();
    } finally {
      // dispatch(LoadingSliceActions.offLoading());
    }
  };

  const restaurantRecommend = useCallback(
    async (e: { value: any; index: number; idx: number }) => {
      let timetable = presetDatas[params?.index];
      try {
        let lat = 0;
        let lng = 0;
        let radius = 2000;
        let status = timetable[e.idx][e.index - 1];
        let goCheck = true;
        if (timetable[e.idx].length == 1) {
        } else {
          if (e.index == timetable[e.idx].length - 1) {
            if (
              timetable[e.idx][timetable[e.idx].length - 2].name.includes(
                ""
              )
            ) {
              goCheck = false;
            } else {
              lat = timetable[e.idx][timetable[e.idx].length - 2].lat;
              lng = timetable[e.idx][timetable[e.idx].length - 2].lng;
              status = timetable[e.idx][timetable[e.idx].length - 2];
            }
          } else if (e.index == 0) {
            if (timetable[e.idx][1].name.includes("")) {
              goCheck = false;
            } else {
              lat = timetable[e.idx][1].lat;
              lng = timetable[e.idx][1].lng;
              status = timetable[e.idx][1];
            }
          } else {
            const departure = {
              lat: timetable[e.idx][e.index - 1].lat,
              lng: timetable[e.idx][e.index - 1].lng,
            };
            const arrival = {
              lat: timetable[e.idx][e.index + 1].lat,
              lng: timetable[e.idx][e.index + 1].lng,
            };
            const distance = Math.ceil(
              useDistance({ departure: departure, arrival: arrival })
            );
            lat =
              (timetable[e.idx][e.index - 1].lat +
                timetable[e.idx][e.index + 1].lat) /
              2;
            lng =
              (timetable[e.idx][e.index - 1].lng +
                timetable[e.idx][e.index + 1].lng) /
              2;
            radius =
              distance >= 20 ? 20000 : distance == 0 ? 2000 : distance * 1000;
            if (
              timetable[e.idx][e.index - 1].name.includes("") &&
              timetable[e.idx][e.index + 1].name.includes("")
            ) {
              goCheck = false;
            } else if (timetable[e.idx][e.index - 1].name.includes("")) {
              status = timetable[e.idx][e.index + 1];
            } else if (timetable[e.idx][e.index + 1].name.includes("")) {
              status = timetable[e.idx][e.index - 1];
            }
          }
          if (goCheck) {
            const startNumber = e.value.y; //  
            const count = e.value.takenTime / 30; //  

            const sequentialArray = Array.from(
              { length: count },
              (_, index) => startNumber + index
            );
            const data = await getRecommendList({
              name: " ",
              x: e.value.x,
              index: e.index,
              y: sequentialArray,
              category: e.value.category,
              lat: lat,
              lng: lng,
              apiCategory: region[0].startsWith("") ? "restaurants" : "FD6",
              radius: radius,
              backupLat: timetable[e.idx][e.index - 1]?.lat ?? 0,
              backupLng: timetable[e.idx][e.index - 1]?.lng ?? 0,
              status: status,
            });
            return data;
          } else {
            // dispatch(
            // 	modalSliceActions.setOpenModal({
            // 		modalTitle: ' .',
            // 		modalSubTitle: ',    \n   ',
            // 	}),
            // );
          }
        }
      } catch (e) {}
    },
    [presetDatas, region]
  );

  const handleAutoRecommend = async ({ item, copy, idx }: any) => {
    try {
      let copy2 = [...item];
      const handleItems = item.map(async (value, index) => {
        if (value.name == " " || value.name == " ") {
          let items = await restaurantRecommend({
            value: value,
            index: index,
            idx: idx,
          });
          if (items?.length != 0) {
            let checks = copy2.filter((checkValue, checkIndex) => {
              items[0].place_name == checkValue.name;
            });
            items = items[checks.length == 0 ? 0 : 1];
            copy2[index] = {
              ...copy2[index],
              name: items.place_name,
              lat: Number(items.y),
              lng: Number(items.x),
              category: value.category,
              x: value.x,
              y: value.y,
              id: Number(items.y) + copy2[index]?.name,
            };
          } else {
            open("      ");
          }
        } else if (value.name == " " && index != 0) {
          let items = await accommodationRecommend({
            value: value,
            index: index,
            idx: idx,
          });
          if (items?.length != 0) {
            items = items[0];
            copy2[index] = {
              ...copy2[index],
              name: items.place_name,
              lat: Number(items.y),
              lng: Number(items.x),
              category: value.category,
              x: value.x,
              y: value.y,
              id: Number(items.y) + copy2[index]?.name,
            };
          } else {
            open("      ");
          }
        } else if (index == 0 && value.name == " ") {
          if (
            copy[idx - 1].at(-1)?.category == value.category &&
            copy[idx - 1].at(-1)?.name != " "
          ) {
            copy2[index] = {
              ...copy2[index],
              name: copy[idx - 1].at(-1)?.name,
              lat: Number(copy[idx - 1].at(-1)?.lat),
              lng: Number(copy[idx - 1].at(-1)?.lng),
              category: value.category,
              x: value.x,
              y: value.y,
              id: Number(copy[idx - 1].at(-1)?.lat) + copy2[index]?.name,
            };
          }
        }
      });
      await Promise.all(handleItems);
      return copy2;
    } catch (e: any) {
    } finally {
      // dispatch(LoadingSliceActions.offLoading());
    }
  };

  const goNext = async (e: boolean) => {
    try {
      let copy = [...presetDatas[params.index]];
      if (presetDatas[params.index].length != nDay + 1) {
        const check = nDay + 1 - presetDatas[params.index].length;
        for (let i = 0; i < check; i++) {
          copy.push([]);
        }
      }
      dispatch(travelSliceActions.setAutoRecommendFlag(e));
      if (e) {
        await copy.reduce(async (prev, item, idx) => {
          await prev;
          const newElem = await handleAutoRecommend({ item, copy, idx });
          copy[idx] = newElem;
        }, Promise.resolve());
      }
      dispatch(travelSliceActions.enrollTimetable(copy));
      navigation.dispatch(
        CommonActions.reset({
          index: 0,
          routes: [{ name: "/timetable" }],
        })
      );
    } catch (err) {
      console.log(err, "");
    }
  };
  const handleAccommodation = () => {
    if (nDay == 0) {
      handleRecommend(false);
    } else {
      bottomSheet.open({
        children: (
          <>
            <Text
              typography="t4"
              fontWeight="bold"
              color={colors.grey800}
              style={{ alignSelf: "center" }}
            >
                {`\n`}?
            </Text>
            <Text
              typography="t5"
              fontWeight="regular"
              color={colors.grey600}
              style={{ textAlign: "center" }}
            >
                 
            </Text>
            <BottomSheet.CTA.Double
              leftButton={
                <Button
                  type="dark"
                  style="weak"
                  display="block"
                  onPress={() => {
                    handleRecommend(false);
                    bottomSheet.close();
                  }}
                >
                  {""}
                </Button>
              }
              rightButton={
                <Button
                  type="primary"
                  style="fill"
                  display="block"
                  onPress={() => {
                    handleRecommend(true);
                    bottomSheet.close();
                  }}
                >
                  {""}
                </Button>
              }
            ></BottomSheet.CTA.Double>
          </>
        ),
      });
    }
  };
  const handleRecommend = (e: boolean) => {
    bottomSheet.open({
      children: (
        <>
          <Text
            typography="t4"
            fontWeight="bold"
            color={colors.grey800}
            style={{ alignSelf: "center" }}
          >
            !
          </Text>
          <Text
            typography="t5"
            fontWeight="regular"
            color={colors.grey600}
            style={{ textAlign: "center" }}
          >
               {`\n`}   
          </Text>
          <BottomSheet.CTA.Double
            leftButton={
              <Button
                type="dark"
                style="weak"
                display="block"
                onPress={() => {
                  bottomSheet.close();
                }}
              >
                {""}
              </Button>
            }
            rightButton={
              <Button
                type="primary"
                style="fill"
                display="block"
                onPress={() => {
                  goNext(e);
                  bottomSheet.close();
                }}
              >
                {""}
              </Button>
            }
          ></BottomSheet.CTA.Double>
        </>
      ),
    });
  };
  const calculateTendency = (e: any) => {
    let copy = [];
    let copy2 = [];
    e?.tendencyNameList?.forEach((item, idx) => {
      if (!["", "", "", ""].includes(item)) {
        copy.push(item);
        copy2.push(e.tendencyRanking[idx]);
      }
    });
    let min = 100;
    let minIndex = -1;
    let nextMin = 100;
    let nextMinIndex = -1;
    console.log(copy, copy2);
    copy2.forEach((item, idx) => {
      if (item <= min) {
        nextMin = min;
        nextMinIndex = minIndex;
        min = item;
        minIndex = idx;
      } else if (item <= nextMin) {
        nextMin = item;
        nextMinIndex = idx;
      }
    });
    let result =
      (e?.tendencyNameList[minIndex] ?? "") +
      (e?.tendencyNameList[nextMinIndex]
        ? ", " + e?.tendencyNameList[nextMinIndex]
        : "");
    return result;
  };
  const onViewableItemsChanged = useRef((items) => {
    setTabalue(String(items?.changed[0]?.index));
  });
  const moveScroll = (e) => {
    scrollRef.current?.scrollToIndex({
      index: Number(e),
      animated: false,
    });
    setTabalue(e);
  };

  const renderItem = ({ item, index }) => {
    return (
      <Stack.Vertical
        style={{
          position: "relative", //   
          borderWidth: 1,
          borderColor: "#eeeeee",
          borderRadius: 13,
          paddingHorizontal: 24,
          paddingVertical: 20,
          marginTop: 10,
          marginHorizontal: 24,
        }}
      >
        <Text typography="t5" fontWeight="medium" color={colors.blue700}>
          {moment(day[index]).format("YY-MM-DD") + " "}(
          {weekdays[moment(day[index]).days()]})
        </Text>
        <View style={{ height: 20 }}></View>
        {item?.map((value, idx) => (
          <StepperRow
            hideLine={idx == item.length - 1}
            left={<StepperRow.NumberIcon number={idx + 1} />}
            center={
              <StepperRow.Texts
                type="A"
                title={value.name + " "}
                description={
                  !value.name.includes("")
                    ? Math.floor(value.takenTime / 60) != 0 &&
                      Math.floor(value.takenTime / 60) +
                        "" +
                        (value.takenTime % 60 != 0
                          ? (value.takenTime % 60) + ""
                          : "")
                    : ""
                }
              />
            }
          />
        ))}
      </Stack.Vertical>
    );
  };
  const [layoutHeights, setLayoutHeights] = useState({
    listRow: 0,
    map: 0,
    tab: 0,
    cta: 0,
  });

  const totalFixedHeight =
    layoutHeights.listRow +
    layoutHeights.map +
    layoutHeights.tab +
    layoutHeights.cta;

  const screenHeight = Dimensions.get("window").height;
  const flatListHeight = screenHeight - totalFixedHeight;

  return (
    <View style={{ flex: 1 }}>
      <NavigationBar />
      <FixedBottomCTAProvider>
        {presetTendencyList[params?.index]?.tendencyNameList.length >= 2 && (
          <ListRow
            left={
              <ListRow.Image
                width={24}
                height={24}
                type="default"
                source={{
                  uri: "https://static.toss.im/2d-emojis/png/4x/u1F31F.png",
                }}
              />
            }
            contents={
              <ListRow.Texts
                type="2RowTypeA"
                top={params?.index + 1 + " "}
                // bottom={calculateTendency(presetTendencyList[params.index])}
                bottom={
                  <Text
                    typography="t6"
                    fontWeight="regular"
                    color={colors.grey600}
                  >
                    <Text
                      typography="t6"
                      fontWeight="regular"
                      color={colors.blue500}
                    >
                      [{calculateTendency(presetTendencyList[params.index])}]
                    </Text>{" "}
                      !
                  </Text>
                }
                topProps={{ color: colors.grey800 }}
                bottomProps={{ color: colors.grey600 }}
              />
            }
          />
        )}
        <CustomMapViewMarker
          presetData={presetDatas[params?.index]}
          selectedIndex={tabValue}
          isWideZoom={false}
        />

        <Tab
          fluid
          size="large"
          onChange={(e) => {
            moveScroll(e);
          }}
          value={tabValue}
          style={{ marginTop: 5 }}
        >
          {[
            ...Array.from(
              { length: presetDatas[params?.index].length },
              (item, index) => index
            ),
          ].map((item, idx) => {
            return <Tab.Item value={String(idx)}>DAY {idx + 1}</Tab.Item>;
          })}
        </Tab>
        <FlatList
          keyExtractor={(_, index) => index.toString()}
          style={{ height: 400 }}
          ref={scrollRef}
          data={presetDatas[params?.index]}
          onScrollToIndexFailed={(info) => {
            setTimeout(() => {
              scrollRef.current?.scrollToIndex({
                index: info.index,
                animated: true,
              });
            }, 500); //    
          }}
          showsVerticalScrollIndicator={false}
          onViewableItemsChanged={onViewableItemsChanged.current}
          viewabilityConfig={{
            itemVisiblePercentThreshold: 80, // 50%   
          }}
          renderItem={renderItem}
        ></FlatList>
        <FixedBottomCTA onPress={handleAccommodation}>
             
        </FixedBottomCTA>
      </FixedBottomCTAProvider>
    </View>
  );
}
