export function shouldUseNativeDriver(key) {
    /**
     * FIXME: backgroundColor     0.72.6  useNativeDriver  true  .
     * react-native  0.68 0.72  ,  false .
     */
    if (key === 'backgroundColor') {
        return false;
    }
    /** color, width, height   JS  . */
    if (key === 'color' || key === 'width' || key === 'height') {
        return false;
    }
    return true;
}
