/**
 * @param items      cycle items
 * @param count  
 * @param { gutter }   
 *
 * @example
 * ```ts
 *  repeatCycle([1, 2], 3, { gutter: 0 });
 *  // [1, 0, 2, 0, 1]
 * ```
 */
export function* repeatCycle(items, count, { gutter } = {}) {
    for (let i = 0; i < count; i++) {
        yield items[i % items.length];
        if (i < count - 1 && gutter != null) {
            yield gutter;
        }
    }
}
