/**
 * @public
 * @category UI
 * @name ColorPreference
 * @description
 *      . ,   
 *
 * @typedef {'light' | 'dark'} ColorPreference
 */
export type ColorPreference = 'light' | 'dark';

/**
 * @name NetworkStatus
 * @description
 *    .
 *
 * @typedef {'WIFI' | '2G' | '3G' | '4G' | '5G' | 'UNKNOWN' | 'OFFLINE'} NetworkStatus
 */
type NetworkStatus = 'WIFI' | '2G' | '3G' | '4G' | '5G' | 'UNKNOWN' | 'OFFLINE';

/**
 * @category Types
 * @name BaseInitialProps
 * @description
 *     
 *
 * @interface
 * @property {'ios' | 'android'} platform -  
 * @property {string} appVersion -  
 * @property {ColorPreference} initialColorPreference -  
 * @property {NetworkStatus} networkStatus -  
 * @property {number} loadingStartTs - ReactNativeView      (timestamp)
 * @property {string} [scheme] -  
 */
type BaseInitialProps = {
  platform: 'ios' | 'android';
  appVersion: string;
  initialColorPreference: ColorPreference;
  networkStatus: NetworkStatus;
  loadingStartTs: number;
  scheme?: string;
};

/**
 * @category Types
 * @name AndroidInitialProps
 * @description
 * Android  React Native   .
 *
 * @interface
 * @augments BaseInitialProps
 * @property {'android'} platform -  (Android)
 * @property {string} initialFontScale -    
 * @property {string} distributionGroup -  
 */
export type AndroidInitialProps = BaseInitialProps & {
  platform: 'android';
  initialFontScale: string;
  distributionGroup: string;
};

/**
 * @category Types
 * @name IOSInitialProps
 * @description
 * iOS    
 *
 * @interface
 * @augments BaseInitialProps
 * @property {'ios'} platform -  (iOS)
 * @property {IOSFontSizeType} initialFontSize -   
 * @property {boolean} isVisible -  
 */
export type IOSInitialProps = BaseInitialProps & {
  platform: 'ios';
  initialFontSize: IOSFontSizeType;
  isVisible: boolean;
};

/**
 * @category Types
 * @name IOSFontSizeType
 * @description
 * iOS     
 *
 * @typedef {'xSmall' | 'Small' | 'Medium' | 'Large' | 'xLarge' | 'xxLarge' | 'xxxLarge' | 'A11y_Medium' | 'A11y_Large' | 'A11y_xLarge' | 'A11y_xxLarge' | 'A11y_xxxLarge'} IOSFontSizeType
 */
type IOSFontSizeType =
  | 'xSmall'
  | 'Small'
  | 'Medium'
  | 'Large'
  | 'xLarge'
  | 'xxLarge'
  | 'xxxLarge'
  | 'A11y_Medium'
  | 'A11y_Large'
  | 'A11y_xLarge'
  | 'A11y_xxLarge'
  | 'A11y_xxxLarge';

/**
 * @public
 * @category 
 * @name InitialProps
 * @description
 * React Native      ,
 *  (Android/iOS)      .
 *         ,      .
 *
 * Android    `AndroidInitialProps`, iOS    `IOSInitialProps` .
 *
 * @property {'ios' | 'android'} platform -     . `ios`  `android`  .
 * @property {ColorPreference} initialColorPreference -   .     .
 * @property {NetworkStatus} networkStatus -       .
 * @property {string} [scheme] -      URL .
 * @property {`xSmall` | `Small` | `Medium` | `Large` | `xLarge` | `xxLarge` | `xxxLarge` | `A11y_Medium` | `A11y_Large` | `A11y_xLarge` | `A11y_xxLarge` | `A11y_xxxLarge`} initialFontSize (iOS only) iOS   .      .   `Large`.
 * @property {boolean} isVisible (iOS only) iOS     .  `true` .
 * @property {string} initialFontScale (Android only) Android   .  Android       .          .
 *
 * @example
 *
 * ### `InitialProps`  
 *
 * ::: code-group
 * ```tsx [_app.tsx]
 * import { PropsWithChildren } from 'react';
 * import { Bedrock, InitialProps } from 'react-native-bedrock';
 * import { context } from '../require.context';
 *
 * const APP_NAME = 'my-app-name';
 *
 * function AppContainer({ children, ...initialProps }: PropsWithChildren<InitialProps>) {
 *   console.log({ initialProps });
 *   return <>{children}</>;
 * }
 *
 * export default Bedrock.registerApp(AppContainer, {
 *   appName: APP_NAME,
 *   context,
 * });
 * :::
 * ```
 */
export type InitialProps = AndroidInitialProps | IOSInitialProps;
