export function sequenceAnimation(animations) {
    return {
        start: () => {
            async function play() {
                for (const animation of animations) {
                    const finished = await animation.start();
                    if (finished === false) {
                        return false;
                    }
                }
                return true;
            }
            return play();
        },
        stop: () => {
            for (const animation of animations) {
                animation.stop();
            }
        },
        reset: () => {
            for (const animation of animations) {
                animation.reset();
            }
        },
        seekProgress: (offset) => {
            for (const animation of animations) {
                animation.seekProgress(offset);
            }
        },
    };
}
