export const format = {
    phoneNumber: {
        transform: transformToPhoneNumber,
        reset: resetFromPhoneNumber,
    },
    price: {
        transform: transformToPrice,
        reset: resetFromPrice,
    },
};
function resetFromPhoneNumber(value) {
    const strValue = String(value);
    return strValue.replace(/\D/g, '');
}
function transformToPhoneNumber(value) {
    const strValue = String(value);
    const sliced = strValue.slice(0, 2) === '02' ? strValue.slice(0, 10) : strValue.slice(0, 11);
    const formatWithFirstMatched = (str, regexps) => {
        for (const regexp of regexps) {
            const phone = makePhoneNumberIfMatched(str, regexp);
            if (phone) {
                return phone;
            }
        }
        return str;
    };
    const makePhoneNumberIfMatched = (str, regexp) => {
        const matched = str.match(regexp);
        if (!matched) {
            return undefined;
        }
        return matched
            .slice(1, 4)
            .filter(grouped => grouped.length > 0)
            .join('-');
    };
    return formatWithFirstMatched(sliced, [
        /^(010)(\d{1,4})(\d{0,4})$/,
        /^(02)(\d{1,3})(\d{0,4})$/,
        /^(02)(\d{1,4})(\d{0,4})$/,
        /^(\d{3})(\d{1,3})(\d{0,4})$/,
        /^(\d{3})(\d{1,4})(\d{0,4})$/,
        /(\d*)/,
    ]);
}
function resetFromPrice(value) {
    const strValue = String(value);
    return String(strValue.replace(/,/g, ''));
}
function transformToPrice(value) {
    const strValue = String(value);
    return strValue.replace(/(\d)(?=(\d\d\d)+(?!\d))/g, '$1,');
}
