import React, { Fragment, type ReactNode } from 'react';

/**
 * @category Constants
 * @name Children
 * @description
 * `Children`        .
 *
 * @property {ChildrenGap} Gap - `Children.Gap`      (gap)     .
 */
export const Children = {
  /**
   * @name Children.Gap
   * @description ReactNode children     gap  .
   */
  Gap: ChildrenGap,
};

interface ChildrenGapProps {
  children: ReactNode;
  gap: ReactNode;
}

/**
 * @category Components
 * @name ChildrenGap
 * @description
 * `Children.Gap`     (gap)       .
 *   ,          UI    .
 *
 * @param {object} props -   `props` .
 * @param {ReactNode} props.children -   .
 * @param {ReactNode} props.gap -     . `gap`  React  .
 *
 * @throws {Error} `gap`  React     . `gap`  `React.Element`  .
 *
 * @example
 * ##    16px     
 *
 * ```tsx
 * import { Children, Spacing } from '@react-native-bedrock/core';
 * import { Text } from 'react-native';
 *
 * function Component() {
 *   return (
 *     <Children.Gap gap={<Spacing size={16} />}>
 *       <Text> </Text>
 *       <Text> </Text>
 *       <Text> </Text>
 *     </Children.Gap>
 *   );
 * }
 * ```
 */
function ChildrenGap({ children, gap }: ChildrenGapProps) {
  if (!React.isValidElement(gap)) {
    throw new Error('gap prop    .');
  }

  return (
    <>
      {React.Children.map(children, (child, index) => {
        const isLastChild = index === React.Children.count(children) - 1;

        return (
          <Fragment key={index}>
            {child}
            {!isLastChild && child != null ? gap : undefined}
          </Fragment>
        );
      })}
    </>
  );
}
