export function getTextFieldStatus(props, focused) {
    if (props.disabled) {
        return 'disabled';
    }
    if (props?.editable === false) {
        return 'disabled';
    }
    if (props.hasError) {
        return 'error';
    }
    if (focused) {
        return 'focused';
    }
    return 'normal';
}
