import React, { useCallback, useEffect } from "react";
import { View } from "react-native";
import { BedrockRoute, useNavigation } from "react-native-bedrock";
import {
  colors,
  Text,
  ListRow,
  Top,
  FixedBottomCTA,
  FixedBottomCTAProvider,
  PartnerNavigation,
  Toast,
  useToast,
  Skeleton,
  AnimateSkeleton,
} from "@toss-design-system/react-native";
import { appLogin } from "@apps-in-toss/framework";
import { useAppDispatch, useAppSelector } from "store";
import {
  socialConnect,
  tossTest,
  tossUser,
  travelSliceActions,
} from "../redux/travle-slice";
export const Route = BedrockRoute("/", {
  validateParams: (params) => params,
  component: Index,
});

export function Index() {
  const navigation = useNavigation();
  const dispatch = useAppDispatch();
  const { userId, userJwtToken } = useAppSelector((state) => state.travelSlice);
  const handleNext = () => {
    if (userId != null) {
      dispatch(
        travelSliceActions.reset({ userId: userId, userJwtToken: userJwtToken })
      );
      navigation.navigate("/enroll/title");
    } else {
      handleLogin();
    }
  };
  const { open } = useToast();
  const handleLogin = useCallback(async () => {
    /**
     * appLogin 
     * -        ,       .       .
     * -             .
     */
    const { authorizationCode, referrer } = await appLogin();
    const userData = await dispatch(
      tossUser({ authorizationCode, referrer })
    ).unwrap();
    if (userData?.resultType == "SUCCESS") {
      await dispatch(socialConnect({ userToken: userData?.success?.userKey }));
      navigation.navigate("/enroll/title");
    } else {
      open("   ", {
        icon: "icon-warning-circle",
      });
    }
    // const a = await dispatch(tossTest({ authorizationCode, referrer }));

    /**
     *  authorizationCode  referrer   .
     */
  }, []);
  // useEffect(() => {
  //   userId || handleLogin();
  // }, [userId]);
  return (
    <View style={{ flex: 1 }}>
      <PartnerNavigation
        title="AI    "
        icon={{
          source: {
            uri: "https://static.toss.im/appsintoss/561/454aa293-9dc9-4c77-9662-c42d09255859.png",
          },
        }}
        rightButtons={
          userId != null
            ? [
                {
                  title: "",
                  id: "travle-list",
                  icon: { name: "icon-plane-mono" },
                  onPress: () => {
                    navigation.reset({
                      index: 1,
                      routes: [{ name: "/" }, { name: "/my-travle-list" }],
                    });
                  },
                },
              ]
            : undefined
        }
      ></PartnerNavigation>
      <FixedBottomCTAProvider>
        <Top
          title={
            <Text typography="t6" fontWeight="medium" color={colors.grey600}>
                 AI !
            </Text>
          }
          subtitle1={
            <Text typography="t3" fontWeight="bold" color={colors.grey900}>
              1 {`\n`}     
            </Text>
          }
        ></Top>

        <ListRow
          left={
            <ListRow.Image
              type="3d-emoji"
              source={{
                uri: "https://static.toss.im/2d-emojis/png/4x/u1F31E.png",
              }}
            />
          }
          contents={
            <ListRow.Texts
              type="2RowTypeA"
              top="   "
              bottom="      "
              topProps={{
                typography: "t5",
                fontWeight: "semiBold",
                color: colors.grey700,
              }}
              bottomProps={{
                typography: "t6",
                fontWeight: "regular",
                color: colors.grey600,
              }}
            />
          }
        />
        <ListRow
          left={
            <ListRow.Image
              type="3d-emoji"
              source={{
                uri: "https://static.toss.im/2d-emojis/png/4x/u23F3.png",
              }}
            />
          }
          contents={
            <ListRow.Texts
              type="2RowTypeA"
              top=" "
              bottom="1        "
              topProps={{
                typography: "t5",
                fontWeight: "semiBold",
                color: colors.grey700,
              }}
              bottomProps={{
                typography: "t6",
                fontWeight: "regular",
                color: colors.grey600,
              }}
            />
          }
        />
        <ListRow
          left={
            <ListRow.Image
              type="3d-emoji"
              source={{
                uri: "https://static.toss.im/2d-emojis/png/4x/u1F4F1.png",
              }}
            />
          }
          contents={
            <ListRow.Texts
              type="2RowTypeA"
              top=" "
              bottom="       "
              topProps={{
                typography: "t5",
                fontWeight: "semiBold",
                color: colors.grey700,
              }}
              bottomProps={{
                typography: "t6",
                fontWeight: "regular",
                color: colors.grey600,
              }}
            />
          }
        />
        <FixedBottomCTA onPress={handleNext}></FixedBottomCTA>
      </FixedBottomCTAProvider>
    </View>
  );
}
